import request from '../request'
export function alarmList(data) {
  return request({
    url: '/logs/list',
    method: 'post',
    data,
    headers:{'content-type':'application/x-www-form-urlencoded'},
    
  })
}
export function alarmListDay(data) {
  return request({
    url: '/logs/page',
    method: 'post',
    data,
    headers:{'content-type':'application/x-www-form-urlencoded'},
    
  })
}
