<template>
  <div id="realTimeMonitoring">
    <div class="bak" v-if="toshow" @click="showMenu"></div>
    <div class="menuShow" :class="{rot:toshow}" v-if="!mediaNone" @click="showMenu">
      <span v-if="!toshow">Open</span>
      <span v-if="toshow">Close</span>
    </div>
    <span class="shrink el-icon-d-arrow-left" :class="{menuHide:!$parent.menuPop}" v-show="!sidePop" @click="sidePop=true"></span>
    <!-- 隐藏侧边栏 -->
    <span class="shrink pop el-icon-d-arrow-right" :class="{menuHide:!$parent.menuPop}" v-show="sidePop" @click="sidePop=false"></span>
    <!-- 显示侧边栏 -->
    <div class="left" :class="{toshow:toshow,sOrh:sidePop}">
      <div class="selectionMode" v-if="mediaNone">
        <div @click="showVideo" :class="[isVideo ? 'isSelectClass' : 'unSelectClass']">
          <div>
            <img src="../../../static/images/videoN.png" alt v-if="isVideo ==false" />
            <img src="../../../static/images/videoY.png" alt v-else />
          </div>
          <span>视频模式</span>
        </div>
        <div @click="showMap" :class="[!isVideo ? 'isSelectClass' : 'unSelectClass']">
          <div>
            <img src="../../../static/images/mapN.png" alt v-if="isVideo ==true" />
            <img src="../../../static/images/mapY.png" alt v-else />
          </div>
          <span>地图模式</span>
        </div>
      </div>
      <div class="showBox">
        <!-- <el-radio v-model="selectedCarOrGun" :label="2">车辆</el-radio>
        <el-radio v-model="selectedCarOrGun" :label="1">枪支</el-radio> -->
        <div @click="selectedCarOrGun=2" :class="{isSelcet:selectedCarOrGun==2}">车辆</div>
        <div @click="selectedCarOrGun=1" :class="{isSelcet:selectedCarOrGun==1}">枪支</div>
        <!-- <div @click="selectedCarOrGun='3'" :class="{isSelcet:selectedCarOrGun=='3'}">北斗终端</div> -->
      </div>

      <div class="selectBox">
        <el-input placeholder="输入关键字过滤(车)" v-model="filterCar" :clearable="true" v-show="selectedCarOrGun==2"></el-input>
        <el-input placeholder="输入关键字过滤(枪)" v-model="filterGun" :clearable="true" v-show="selectedCarOrGun==1"></el-input>
        <el-input placeholder="输入关键字过滤(北斗终端)" v-model="filterTerminal" :clearable="true" v-show="selectedCarOrGun==3"></el-input>
      </div>
      <div class="trailBox">
        <div>实时轨迹</div>

        <el-switch v-model="realTimeTrailValue" class="realTimeTrail"></el-switch>
      </div>
      <div class="treeBox">
        <my-tree :data="data" @node-contextmenu="rightClick" show-checkbox ref="tree" node-key="id" :rend1er-after-expand="false" :default-expand-all="false" :props="defaultProps" @check="getCheckedNodes" @node-click="selectVideoCar" :default-expanded-keys="defaultSelect" @doubleClick="doubleClickTreeNode" :render-content="renderContent" v-show="selectedCarOrGun==2" :expand-on-click-node="false" :filter-node-method="filterNode"></my-tree>

        <div id="handelmenu" v-show="menuVisible" @mouseleave="menuVisible=!menuVisible">

          <el-card class="box-card">
            <div class="text item">
              <el-link :underline="false" @click="rightClickEvent(3)">远程控制停车</el-link>
            </div>
            <div class="text item">
              <el-link :underline="false" @click="rightClickEvent(4)">远程恢复行驶</el-link>
            </div>
            <!-- <div class="text item">
              <el-link :underline="false" @click="rightClickEvent(3)">关闭电源</el-link>
            </div>
            <div class="text item">
              <el-link :underline="false" @click="rightClickEvent(4)">打开电源</el-link>
            </div> -->
            <div class="text item">
              <el-link :underline="false" @click="rightClickEvent(5)">重启设备</el-link>
            </div>
          </el-card>

        </div>
        <my-tree :data="gunData" show-checkbox ref="treeGun" node-key="id" :render-after-expand="false" :default-expand-all="false" :props="defaultPropsGun" @check="getCheckedNodesGun" :render-content="renderContent" :default-expanded-keys="defaultSelectGun" v-show="selectedCarOrGun==1" :expand-on-click-node="false" :filter-node-method="filterNode"></my-tree>
        <my-tree :data="beidouTerminalData" show-checkbox ref="treeBeidouTerminal" node-key="id" :render-after-expand="false" :default-expand-all="false" :props="defaultPropsTerminal" @doubleClick="doubleClickTerminalTreeNode" @check="getCheckedNodesTerminal" :default-expanded-keys="defaultSelectTerminal" v-show="selectedCarOrGun==3" :expand-on-click-node="false" :filter-node-method="filterNode"></my-tree>
      </div>
      <div class="consistency" v-if="mediaNone">
        <el-collapse v-model="activeNames">
          <el-collapse-item title="对讲监听" name="1">
            <div class="content">
              <div class="selectionMode selectionMode1" v-if="mediaNone">
                <div @click="changeRadio('1')" :class="[radio=='1' ? 'isSelectClass' : 'unSelectClass']">
                  <div>
                    <img src="../../../static/images/talkN.png" alt v-if="!(radio==1)" />
                    <img src="../../../static/images/talkY.png" alt v-else />
                  </div>
                  <span>对讲</span>
                </div>
                <div @click="changeRadio('2')" :class="[!(radio=='1') ? 'isSelectClass' : 'unSelectClass']">
                  <div>
                    <img src="../../../static/images/listenN.png" alt v-if="radio==1" />
                    <img src="../../../static/images/listenY.png" alt v-else />
                  </div>
                  <span>监听</span>
                </div>
              </div>
              <div class="carChnBox">
                <div class="carChnLabel">车辆</div>
                <div calss="carChnMes"> {{ talkAndMonitor.licenseNo==null?" ":talkAndMonitor.licenseNo+'('+talkAndMonitor.imei+')'}}</div>

              </div>
              <div class="carChnBox" v-show="radio=='2'">
                <div class="carChnLabel">通道</div>

                <div class="carChnMes"> {{talkAndMonitor.chn == null? " " : 'CH'+(talkAndMonitor.chn+1)}}</div>
              </div>
              <div class="talkListenStart" @click="open" v-show="!openState">开启</div>
              <div class="talkListenEnd" @click="stop" v-show="openState">停止</div>
              <div class="talkhint">{{talkhintText}}</div>
            </div>
          </el-collapse-item>
        </el-collapse>
      </div>
    </div>
    <div class="content">
      <div id="videoBox" v-show="isVideo&&mediaNone" :class="{[winChange]:true,[winResize]:true}">
        <div id="video" ref="video" :class="{[winChange]:true,[winResize]:true}"></div>
        <img class="stopAllVideo" title="暂停所有视频" src="../../../static/images/stop.png" @click="stopAllVideo" />
      </div>
      <div id="map" ref="map" :class="[winChange]"></div>
      <div id="bottomBox" v-if="mediaNone" :class="[winChange]">
        <div class="iconBox">
          <el-tooltip class="item" effect="dark" content="最小化" placement="top-start">
            <i class="el-icon-minus myicons" @click="hiddenBootomBox"></i>
          </el-tooltip>
          <el-tooltip class="item" effect="dark" content="窗口化" placement="top-start">
            <i class="el-icon-copy-document myicons" @click="shwoBootomBox(null)"></i>
          </el-tooltip>
          <el-tooltip class="item" effect="dark" content="最大化" placement="top-start">
            <i class="iconfont iconmaximize myicons" @click="maxBootomBox(null)"></i>
          </el-tooltip>
        </div>
        <el-badge class="mark" :value="newWaringData.length" v-if="newWaringData.length" :max="199" />
        <el-badge class="mark1" :value="beidouTerminalMes.length" v-if="beidouTerminalMes.length" :max="199" />

        <!-- 固定的头部按钮和底部分页器 -->
        <div v-show="upOrDown=='down'" class="fixedBar">
          <!-- 报警 -->
          <div class="alarmBtnBox topBtn" v-show="activeName=='first'">
            <!--
              暂时隐藏这两个功能
               <el-button type="primary" @click="handleAlarm" :disabled="selectAlarmObjs.length == 0 ? true : false">批量处理</el-button>
            <el-button type="primary" @click="delayAlarm" :disabled="selectAlarmObjs.length == 0 ? true : false">批量延迟</el-button> -->
          </div>
          <el-pagination background layout="prev, pager, next, sizes, total, jumper" :page-sizes="[5, 10, 15, 20]" :page-size="pagesize" :total="newWaringData.length" v-if="newWaringData.length&&activeName=='first'" :current-page="currpage" @current-change="handleCurrentChange" @size-change="handleSizeChange"></el-pagination>
          <!--  网点 -->
          <div class="networkHeader topBtn" v-show="activeName=='two'">
            <div class="filter-container">
              <el-select v-model="networkVal" class="filter-item" placeholder="请选择网点类型" @change="handleFilter">
                <el-option v-for="item in networkList" :key="item.type" :label="item.option" :value="item.type" />
              </el-select>
            </div>
            <div class="bottomLargeBtn" @click="showAllNetwork()">显示全部</div>
            <div class="bottomLargeBtn" @click="deleteAllNetwork()">隐藏全部</div>
          </div>
          <el-pagination background layout="prev, pager, next, sizes, total, jumper" :page-sizes="[5, 10, 15, 20]" :page-size="pagesize3" :total="list_network.length" v-if="list_network.length&&activeName=='two'" :current-page="currpage3" @current-change="handleCurrentChange3" @size-change="handleSizeChange3"></el-pagination>
          <!-- 路线 -->
          <div class="networkHeader topBtn" v-show="activeName=='three'">
            <div class="bottomLargeBtn" @click="deleteRoute()">隐藏全部路线</div>
          </div>
          <pagination v-if="total_route>0&&activeName=='three'" :total="total_route" :page.sync="listQuery_route.page" :limit.sync="listQuery_route.limit" @pagination=" getRouteList" @current-change="handleCurrentChange4" />
          <!-- 离线超时 -->
          <div class="networkHeader topBtn" v-show="activeName=='four'">
            <el-radio-group v-model="offlineType" @change="getOffline" class="carOrGunRadio">
              <el-radio :label="2">车</el-radio>
              <el-radio :label="1">枪</el-radio>
            </el-radio-group>
          </div>
          <el-pagination background layout="prev, pager, next, sizes, total, jumper" :page-sizes="[5, 10, 15, 20]" :page-size="pagesize1" :current-page="currpage2" v-if="offlineTimeoutArr.length&&activeName=='four'" :total="offlineTimeoutArr.length" @current-change="handleCurrentChange1" @size-change="handleSizeChange1"></el-pagination>

          <!-- 终端设备信息 -->
          <div class="networkHeader topBtn" v-show="activeName=='five'">
            <el-button class="bottomLargeBtn" type="primary" @click="sendMes()">发送消息</el-button>
          </div>
          <el-pagination background layout="prev, pager, next, sizes, total, jumper" :page-sizes="[5, 10, 15, 20]" :page-size="pagesize2" :current-page="currpage2" v-if="beidouTerminalMes.length&&activeName=='five'" :total="beidouTerminalMes.length" @current-change="handleCurrentChange2" @size-change="handleSizeChange2"></el-pagination>
        </div>

        <el-tabs v-model="activeName" class="activeName" @tab-click="textfunctiong" :class="{'activeHeight':activeHeight}" v-show="isTrajectory">
          <el-tab-pane label="报警信息" class="red alarmTab" name="first">
            <el-table :data="newWaringData.slice((currpage - 1) * pagesize, currpage * pagesize)" :key="tableKey" v-loading="listLoading" @selection-change="selectChangeAlarm" ref="multipleTable" highlight-current-row style="width: 99%;" :row-class-name="tableRowClassName" :row-key="getRowKey">
              <el-table-column type="selection" :selectable="selectInit" :reserve-selection="true" width="55"></el-table-column>
              <el-table-column fixed label="序号" prop="id" sortable="custom" align="center" width="80" type="index"></el-table-column>
              <el-table-column label="车牌号或枪号" width="200px" align="center">
                <template slot-scope="{row}">
                  <span>{{ $store.state.realTimeMonitor.carAndGunObj[row.imei]?$store.state.realTimeMonitor.carAndGunObj[row.imei].code:''}}</span>
                </template>
              </el-table-column>
              <el-table-column label="设备号" width="200px" align="center">
                <template slot-scope="{row}">
                  <span>{{ row.imei}}</span>
                </template>
              </el-table-column>
              <!-- <el-table-column label="uuid" width="200px" align="center">
                <template slot-scope="{row}">
                  <span>{{ row.info.uuid}}</span>
                </template>
              </el-table-column>
              -->
              <el-table-column label="设备类型" width="200px" align="center">
                <template slot-scope="scope">
                  <span>{{formatCarOrGunType(scope.row)}}</span>
                </template>
              </el-table-column>
              <el-table-column label="开始时间" width="200px" align="center">
                <template slot-scope="scope">
                  <span>{{formatDate(parseInt(scope.row.info.time) ) }}</span>
                </template>
              </el-table-column>
              <el-table-column label="最新报警" min-width="200px" align="center">
                <template slot-scope="scope">
                  <span>{{getWarningType(scope.row,scope.row.type)}}</span>
                </template>
              </el-table-column>
              <!-- <el-table-column label="报警状态" min-width="200px" align="center">
                <template slot-scope="scope">
                  <span
                    v-if="scope.row.isCancled&&(scope.row.status!==10007||(scope.row.status!==10007&&scope.row.type==1))"
                  >已取消</span>
                  <el-button
                    v-if="scope.row.status==10011&&scope.row.isCancled==false"
                    type="warning"
                    size="mini"
                    @click="cancle(scope.row.imei)"
                  >取消报警</el-button>
                </template>
              </el-table-column>-->
              <el-table-column label="操作" min-width="200px" align="center" fixed="right">
                <template slot-scope="scope">
                  <el-button v-if="scope.row.status==10011&&scope.row.isCancled==false" type="primary" size="mini" class="bottomUnActiveBtn" @click="cancle(scope.row.imei)">取消报警</el-button>
                  <el-button type="primary" @click="handleAlarm1(scope.row)" v-show="scope.row.info.uuid" size="mini" class="bottomUnActiveBtn">处理</el-button>
                  <el-button type="primary" size="mini" class="bottomUnActiveBtn" @click="delayAlarm1(scope.row)" v-show="scope.row.info.uuid">延迟</el-button>
                </template>
              </el-table-column>
              <el-table-column label="跟踪" min-width="180px" align="center">
                <template slot-scope="scope">
                  <el-button type="primary" size="mini" @click="checkNodes(scope.row,scope.row.imei,true)" v-show="followCarOrGun!==scope.row.imei&&$store.state.realTimeMonitor.carAndGunObj[scope.row.imei]" class="bottomUnActiveBtn">跟踪</el-button>
                  <el-button type="warning" size="mini" v-show="followCarOrGun==scope.row.imei&&$store.state.realTimeMonitor.carAndGunObj[scope.row.imei]" @click="checkNodes(scope.row,scope.row.imei,false)" class="bottomActiveBtn">跟踪中</el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="网点信息" name="two" class="networkPane">
            <el-table key="2" v-loading="listLoading_network" :data="list_network.slice((currpage3 - 1) * pagesize3, currpage3 * pagesize3)" highlight-current-row style="width: 99%;" :row-class-name="tableRowClassName">
              <el-table-column fixed label="序号" prop="id" sortable="custom" align="center" width="60" type="index" :index="indexMethod"></el-table-column>
              <el-table-column label="ID" width="100px" v-if="power==1" align="center" prop="id"></el-table-column>
              <el-table-column label="网点名称" width="400px" align="center" prop="name"></el-table-column>
              <el-table-column label="网点地址" width="400px" align="center" prop="address"></el-table-column>

              <el-table-column label="建立时间" width="160px" align="center" prop="createTime"></el-table-column>
              <el-table-column label="网点类型" width="160px" align="center" prop="networkType"></el-table-column>

              <el-table-column fixed="right" label="操作" align="center" min-width="230" class-name="small-padding fixed-width">
                <template slot-scope="{row}">
                  <el-button type="primary" size="mini" @click="checkNetwork(row)" v-if="row.check==1" class="bottomUnActiveBtn">查看</el-button>
                  <el-button type="warning" size="mini" @click="deleteNetwork(row)" v-if="row.check==0" class="bottomActiveBtn">隐藏</el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="路线信息" name="three">
            <el-table v-loading="listLoading" :data="list_route" fit highlight-current-row style="width: 99%;" :row-class-name="tableRowClassName">
              <el-table-column label="序号" prop="id" align="center" width="60" type="index" :index="indexMethod_route"></el-table-column>
              <el-table-column label="路线名称" min-width="150px" align="center" prop="name"></el-table-column>
              <el-table-column label="建立时间" width="200px" align="center" prop="createTime"></el-table-column>

              <el-table-column label="操作" align="center" min-width="230" class-name="small-padding fixed-width">
                <template slot-scope="{row}">
                  <el-button type="primary" size="mini" @click="checkRoute(row)" :disabled="!row.check" class="bottomUnActiveBtn" v-if="row.check">查看</el-button>
                  <el-button type="warning" size="mini" @click="deleteSingleRoute(row)" v-if="!row.check" class="bottomActiveBtn">隐藏</el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="离线超时信息" name="four" v-if="power==1">
            <el-table :data="offlineTimeoutArr.slice((currpage1 - 1) * pagesize1, currpage1 * pagesize1)" @selection-change="handleSelectionChange" :key="tableKey" v-loading="listLoading" fit highlight-current-row style="width: 99%;" :row-class-name="tableRowClassName">
              <el-table-column label="序号" prop="id" sortable="custom" align="center" width="80" type="index"></el-table-column>
              <el-table-column label="车牌号或枪号" width="200px" align="center">
                <template slot-scope="{row}">
                  <span>{{ $store.state.realTimeMonitor.carAndGunObj[row.imei]?$store.state.realTimeMonitor.carAndGunObj[row.imei].code:''}}</span>
                </template>
              </el-table-column>
              <el-table-column label="设备号" width="200px" align="center">
                <template slot-scope="{row}">
                  <span>{{ row.imei}}</span>
                </template>
              </el-table-column>
              <el-table-column label="设备类型" width="200px" align="center">
                <template>
                  <span>{{offlineType==2?'车':'枪'}}</span>
                </template>
              </el-table-column>
              <el-table-column label="离线时间" width="200px" align="center">
                <template slot-scope="scope">
                  <span>{{formatDate(parseInt(scope.row.time) ) }}</span>
                </template>
              </el-table-column>
              <el-table-column label="离线时长" min-width="200px" align="center">
                <template slot-scope="scope">
                  <span>{{scope.row.offlineTime}}</span>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <!-- <el-tab-pane label="终端设备信息" name="five">
            <el-table :data="beidouTerminalMes.slice((currpage2 - 1) * pagesize2, currpage2 * pagesize2)" @selection-change="handleSelectionChange" :key="tableKey1" v-loading="listLoading" fit highlight-current-row style="width: 99%;" :row-class-name="tableRowClassName">
              <el-table-column label="序号" prop="id" sortable="custom" align="center" width="80" type="index"></el-table-column>

              <el-table-column label="发送id" width="200px" align="center">
                <template slot-scope="{row}">
                  <span>{{ row.id}}</span>
                </template>
              </el-table-column>
              <el-table-column label="发送时间" width="200px" align="center">
                <template slot-scope="scope">
                  <span>{{formatDate(parseInt(scope.row.time) ) }}</span>
                </template>
              </el-table-column>

              <el-table-column label="经纬度" min-width="200px" align="center">
                <template slot-scope="scope">
                  <span v-if="scope.row.lng&&scope.row.lat">{{'经度:'+scope.row.lng+' 纬度:'+scope.row.lat}}</span>
                </template>
              </el-table-column>
              <el-table-column label="发送内容" min-width="200px" align="center">
                <template slot-scope="scope">
                  <span>{{scope.row.content}}</span>
                </template>
              </el-table-column>
              <el-table-column label="操作" align="center" min-width="230" class-name="small-padding fixed-width">
                <template slot-scope="{row}">
                  <el-button type="primary" size="mini" class="sendBtn" @click="sendMes(row)">发送消息</el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane> -->
        </el-tabs>
      </div>
    </div>
    <el-dialog title="发送消息" class="realTimeDialog" :visible.sync="dialogSendVisible" :close-on-click-modal="false" center>
      <el-form :model="sendForm" status-icon :rules="sendRules" ref="sendForm">
        <el-form-item label="设备ID" prop="id">
          <el-input type="text" placeholder="请填数字" v-model="sendForm.id" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="内容" prop="formatContent">
          <el-input type="textarea" v-model="sendForm.formatContent"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitSendMes()">确定</el-button>
      </div>
    </el-dialog>
    <el-dialog :title="dealOrDelayTitle" :visible.sync="dealVisible" append-to-body :close-on-click-modal="true" class="alarmDealBox realTimeDialog" center>
      <el-form ref="dataForm" :model="dealTemp" label-position="left" :rules="rules" center>
        <el-form-item label="处理意见" prop="info">
          <el-input type="textarea" v-model="dealTemp.info" />
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="handleAlarmSubmit()">确定</el-button>
      </div>
    </el-dialog>
    <el-dialog :title="dealOrDelayTitle" :visible.sync="innerDelayVisible" append-to-body :close-on-click-modal="true" class="alarmDelayBox realTimeDialog" center>
      <el-form ref="delayForm" :rules="rules" :model="delayTemp" label-position="left">
        <el-form-item label="延迟时间(分钟)" prop="time">
          <el-input v-model.number="delayTemp.time" />
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="delayOneAlarmSubmit()">确定</el-button>
      </div>
    </el-dialog>
    <!-- <el-dialog title="密码" :visible.sync="pasVisible" append-to-body :close-on-click-modal="true" center>
      <el-form ref="pasForm" :rules="pasRules" :model="pasTemp" label-position="left">
        <el-form-item label="用户密码" prop="pas">
          <el-input v-model="pasTemp.pas" />
        </el-form-item>
      </el-form>
