  watch: {
    //实时推送数据的监控
    "$store.state.realTimeMonitor.newSocketData": {
      handler: function (newer, older) {
        let that = this, fake = 1, //fake两个状态 1：在线 5：显示为黄色的fake状态 6:在线无坐标（无经纬度或经纬度为0）
          //实时推送的数据
          newSocketData = JSON.parse(
            JSON.stringify(that.$store.state.realTimeMonitor.newSocketData)
          ), newSocketDataMes = newSocketData.message, newSocketDataStatus = newSocketData.status;
        if (JSON.stringify(newSocketData) == "{}") {
          return;
        }
        //上线
        if (newSocketDataStatus == 10000) {
          // if(newSocketData.message.imei=='5010629'){
          //   console.warn(newSocketData.message)
            
          // }
          // iconxinhaoge0:无卡 iconxinhaoge1:无信号 iconxinhaoge2:1格 iconxinhaoge3:2格 iconxinhaoge5:3格 iconxinhaoge5:4格 iconxinhaoge6:5格
          let mesImei = newSocketDataMes.imei, mesNetPower = newSocketDataMes.netPower, mesHardDisk = newSocketDataMes.hardDisk;
          //mesNetPower为6的跟为5的显示成一样(最优信号)
          if(mesNetPower==6){
          mesNetPower=5;
          }
        
          //mesNetPower 为-1显示为无信号图标 为0显示无卡图标
          if(mesNetPower!==0&&mesNetPower!==-1){
          mesNetPower+=1;//为了让无信号的时候显示一格，其他的依次类推多显示一格
          }
          //24小时定位的mesNetPower为-1，显示成无信号图标
          if(mesNetPower==-1){
          mesNetPower=1;
          }
          //处理网络信号强度
          if (mesNetPower !== undefined && $(".s" + mesImei).length && $(".s" + mesImei).attr("class").indexOf("iconxinhaoge" + mesNetPower) < 0) {

            $(".s" + mesImei).attr(
              "class",
              "iconfont signal" +
              " iconxinhaoge" +
              mesNetPower +
              " s" +
              mesImei
            );
          }
         
          //处理硬盘状态  
          let diskColor;
          if (mesHardDisk == 0) {
            diskColor = "transparent"
          } else if (mesHardDisk == 1) {
            diskColor = "#ccc"
          }
          else if (mesHardDisk == 2) {
            diskColor = "#3C7EFE"
          }
          diskColor ? $('.iconcloud-disk.d' + mesImei).css('color', diskColor) : '';
          // 处理通道实时视频和录像状态
          this.changeChnColor(newSocketDataMes.cameraLossing, newSocketDataMes.videoLossing, mesImei);
          //无坐标的暂时当作gps信号丢失处理
           if (!newSocketDataMes.lng || !newSocketDataMes.lat) {
            fake=3;

            newSocketDataMes.exType=1;
          }
         
          // if (!newSocketDataMes.lng || !newSocketDataMes.lat) {
          //   fake=6;
          //   newSocketDataMes.exType=6;
          //}
          //默认款箱数量和rfids
          // if(!newSocketDataMes.number){
          // newSocketDataMes.number=0;
          // }
          // if(!newSocketDataMes.rfids){
          // newSocketDataMes.rfids="";
          // }
          //默认速度
          if (!newSocketDataMes.speed) {
            newSocketDataMes.speed = 0;
          }
          //电量处理
          if (newSocketDataMes.type == 1 && newSocketDataMes.battery < 0) {
            newSocketDataMes.battery = null;
          }
          

          /* 如果newSocketData.message.fake=1后端算法是枪支使用车辆的坐标
               但是做了权限处理，管理员会正常显示fake状态（黄色），普通用户会
               显示成正常的离线或者上线状态 （注：都只是设备在地图上颜色的区别
               ，实际坐标都是显示的fake坐标）*/
          //fake枪车的坐标被车代理了，因此速度使用的是车的速度，但是实际上显示枪速度时都乘以了1.8,因此为了fake枪的速度显示正常，需要fake枪的速度都除以1.8
          if (newSocketDataMes.fake == 1) {
            let _data = { ...newSocketDataMes };
            let speed = _data.speed ? _data.speed / 1.852 : 0;
            _data.speed = speed;
            newSocketDataMes = _data;
          }
          //如果是fake坐标并且是管理员，则fake=5,设备显示成黄色状态
          /* 暂时关闭fake功能
          if (newSocketDataMes.fake == 1 && this.power == 1) {
            fake = 5;
          } */
          if (newSocketDataMes.speed == 0) {
            that.speedObj[mesImei] = 0;
          } else {
            that.speedObj[mesImei] = 1;

          }
          //改变左侧树状结构对应设备为在线或者是fake状态
          this.changeInlineStatus(
            newSocketDataMes,
            fake,
            newSocketDataMes.type
          );
          //画实时轨迹
          if (this.realTimeTrailValue == true) {
            let newdata = newSocketDataMes;
            //如果没有选择车辆也没有选择枪支就不画线
            if (this.selectGunArray.length !== 0 || this.selectCarArray.length !== 0) {
              //newdata.reportType的坐标不画线
              if (newdata.reportType !== 3) {

                this.addRealtimeTrail(newdata, newdata.type, fake);
              }
            }
            //添加设备图标
          } else {
            // if (this.addStatus == 2) {
            that.addSelected(newSocketData, fake);
            // }
          }
        } else if (newSocketDataStatus == 10002) {
          //gps信号丢失位置（车辆的离线用10001判断）
          //10002推送包括两个状态：
          // newSocketDataMes.exType为 0：GPS信号丢失（下线） 1：打开信号丢失（网络异常）
          for (let i = 0; i < newSocketDataMes.infos.length; i++) {
            let infoI = newSocketDataMes.infos[i], infoIType = newSocketDataMes.infos[i].type, infoIImei = newSocketDataMes.infos[i].imei;
            //暂时先屏蔽 if (infoIType == 2) {}中代码，吕工说gps信号丢失时也显示信号图标
         /*    if (infoIType == 2) {
              //离线时无信号
              if (
                $(".s" + infoIImei).length &&
