              // $('.iconcloud-disk.d'+infoIImei).css('color',"#ccc")
              // }


              $('.iconcloud-disk.d' + infoIImei).css('color', "transparent");
              //视频丢失和录像状态改变
              $('.iconshishishipin.c' + i + 's' + infoIImei).css('color', '#ccc')
              $('.iconluxiang.c' + i + 's' + infoIImei).css('color', '#ccc')
            } */
            // let gpsType = 2; //2： 离线 3：网络异常
            let gpsType = 3; //修改为不区分离线还是网络异常统一用3表示gps信号丢失
            newSocketDataMes.exType=1;//修改为不区分离线还是网络异常都修改为1表示gps信号丢失

            //newSocketDataMes.exType没有值的时候给一个默认值
            // if (newSocketDataMes.exType == undefined) {
            //   newSocketDataMes.exType = 0;
            // }
            if (newSocketDataMes.exType == 1) {
              gpsType = 3;
            }
            this.changeInlineStatus(infoI, gpsType, infoIType);

            //初始化数据开始
            infoI.lng = null; infoI.lat = null; infoI.exType = newSocketDataMes.exType;
            let obj = { status: newSocketDataStatus, message: infoI };

            //初始化数据结束
            if (this.realTimeTrailValue == true) {
              //改变轨迹图标（此时不画线，只是改变轨迹图标的颜色）
              this.addRealtimeTrail(
                infoI,
                infoIType,
                gpsType
              );
            } else {
              //在地图上添加设备图标或者是改变图标
              // if (that.addStatus == 2) {
              that.addSelected(obj, gpsType);
              // }
            }
          }
        }
        // else if (newSocketDataStatus == 10010) {
        //   //枪支和车辆绑定关系协议
        //   let message = newSocketDataMes.relation;
        //   this.bindCarandgun = {}; //每一次推送的数据都需要将绑定关系重置，所有设备重新计算绑定关系
        //   for (let i = 0; i < message.length; i++) {
        //     if (!this.bindCarandgun.hasOwnProperty(message[i].car)) {
        //       this.bindCarandgun[message[i].car] = [];
        //     }
        //     if (!this.bindCarandgun.hasOwnProperty(message[i].device)) {
        //       this.bindCarandgun[message[i].device] = [];
        //     }
        //     let carimei = this.carAndGunObj[message[i].car]
        //       ? this.carAndGunObj[message[i].car].code
        //       : message[i].car;
        //     let gunimei = this.carAndGunObj[message[i].device]
        //       ? this.carAndGunObj[message[i].device].code
        //       : message[i].device;
        //     this.bindCarandgun[message[i].device].push(carimei);
        //     this.bindCarandgun[message[i].car].push(gunimei);
        //   }
        // } 
        else if (newSocketDataStatus == 10001) {
          //车辆上线离线（目前只用离线状态），10002协议也会用来判断车辆离线
          let gpsType = 2; //2： 离线
          //离线状态改变左侧树状结构和车辆在线数量
          if (newSocketDataMes.status == 0 || !newSocketDataMes.status) {
            newSocketDataMes.status = 0;//看到离线的时候status不错在的情况
            //离线时无信号
            if (
              $(".s" + newSocketDataMes.imei).length &&
              $(".s" + newSocketDataMes.imei)
                .attr("class")
                .indexOf("iconxinhaoge1") < 0
            ) {
              $(".s" + newSocketDataMes.imei).attr(
                "class",
                "iconfont signal" +
                " iconxinhaoge1" +
                " s" +
                newSocketDataMes.imei
              );
            }
            //离线硬盘状态变为断电
            // if($('.iconcloud-disk.d'+newSocketDataMes.imei).css('color')!=="rgba(0, 0, 0, 0)"){
            //   $('.iconcloud-disk.d'+newSocketDataMes.imei).css('color',"#ccc")
            //   }
            for (let d = 0; d < 9; d++) {
              $('.iconcloud-disk.d' + newSocketDataMes.imei).css('color', "transparent");
              //视频丢失和录像状态改变
              $('.iconshishishipin.c' + d + 's' + newSocketDataMes.imei).css('color', '#ccc')
              $('.iconluxiang.c' + d + 's' + newSocketDataMes.imei).css('color', '#ccc')
            }

            this.changeInlineStatus(newSocketDataMes, gpsType, 2);
            //初始化数据开始
            newSocketDataMes.lng = null;
            newSocketDataMes.lat = null;
            newSocketDataMes.exType = 0; //exType 0：离线 1：网络异常 3：电量为0 其他：在线

            let obj = {
              status: newSocketDataStatus,
              message: newSocketDataMes
            };
            //初始化数据结束

            if (this.realTimeTrailValue == true) {
              //改变轨迹图标（此时不画线，只是改变轨迹图标的颜色）
