          }
        } else if (newSocketDataStatus == 10011) {
          //一键报警跟踪
          let checkData = {};
          checkData.status = newSocketDataStatus;
          checkData.info = {};
          checkData.info.type = 2;
          this.checkNodes(checkData, newSocketDataMes.imei, true);
