      // this.delRealTimeTrail();
      // //清空树状接口选中设备回到初始状态
      // this.fileBlur();
      // this.fileBlurGun();
      this.myRichMarker.hide();

      if (n == true) {
        // this.addStatus == 1;
        let makerIconArr = [...this.makerIconArr]; //实时位置被清空前复制实时位置信息
        this.delMakerIcon(); //清空marker信息
        this.posToTrailAddStartMarker(makerIconArr); //初始化实时轨迹并添加起点
      } else {
        this.getSelectObj(); //获取所有被选择的设备
        this.delMakerIcon(); //清空marker信息
        for (let key in this.carMkObj) {
          let type;
          if (this.carMkObj[key].type == 2) {
            type = this.carIsOnlineArr[this.carMkObj[key].message.imei];
          } else if (this.carMkObj[key].type == 1) {
            type = this.gunIsOnlineArr[this.carMkObj[key].message.imei];
          }
          this.addCarToMap(this.carMkObj[key].message, type);
        }
        this.getCarOrGunLastGps(1, "trailToPos"); //显示枪(如果实时轨迹存在这个设备的图标，则只是重新初始化makerIconArr的信息，否则新增图标)
        this.getCarOrGunLastGps(2, "trailToPos"); //显示车(如果实时轨迹存在这个设备的图标，则只是重新初始化makerIconArr的信息，否则新增图标)
        this.delRealTimeTrail("notEmptyCarOrGun"); //清空轨迹相关(但是不清空已经显示在地图上的小车或者是枪支，目的是为了避免清空图标以后重新添加图标导致的短时间图标消失)
        // this.addStatus == 2;
      }
