    //监听报警
    "$store.state.realTimeMonitor.warningData": {
      handler: function (newer, older) {
        let that = this;
        if (newer) {
          that.newWaringData = newer;
          //改变地图上设备状态（上线离线报警电量为0等）
          that.changeToRedAlarm();
          that.changeInlineStatus(null, null, 2, true);
          that.changeInlineStatus(null, null, 1, true);
          //当报警数量改变时更改当前页
          if (
            that.currpage >
            (that.newWaringData.length - 1) / that.pagesize + 1 &&
            that.newWaringData.length
          ) {
            that.currpage -= 1;
          }
          if (sessionStorage.uuidObj) {
            this.uuidObj = JSON.parse(sessionStorage.uuidObj);
          }

          this.selectRow.forEach((item, index) => {


            if (!this.uuidObj.hasOwnProperty(item.info.uuid)) {


              that.$refs.multipleTable.toggleRowSelection(item, false);
            }
          });
          this.$forceUpdate();
        }
      },
      deep: true,
      immediate: true,
    },
    //监听设备与车牌号对应关系改变，对应的显示也应该要改变
    "$store.state.realTimeMonitor.carAndGunObj": {
      handler: function (newer, older) {
        let that = this;
        if (newer) {
          this.carAndGunObj = this.$store.state.realTimeMonitor.carAndGunObj;
          this.$forceUpdate();
        }
      },
      deep: true
    },
    // 监听北斗短报文
    "$store.state.realTimeMonitor.beidouTerminalMes": {
      handler: function (newer, older) {
        if (newer.length) {
          this.beidouTerminalMes = newer;
          //北斗终端的最新一条消息总是放在最前面的
          //当监听到$store.state.realTimeMonitor.beidouTerminalMes改变时
          //实际上是$store.state.realTimeMonitor.beidouTerminalMes里面unshift进去最新的一个短报文消息
          //因此如果要获得最新的消息只需要取this.beidouTerminalMes[0]就好了
          if (this.beidouTerminalMes.length) {
            let has = false; //是否存在需要被替换成最新消息的终端标志
            let beidouTerminalData1 = [...this.beidouTerminal];

            beidouTerminalData1.forEach((item, index) => {
              if (item.id == this.beidouTerminalMes[0].id) {
                has = true;
                this.beidouTerminal[index] = this.beidouTerminalMes[0]; //被替换成最新的消息
                //如果终端设备被勾选，则更新设备信息
                for (let j = 0; j < this.selectTerminal.length; j++) {
                  if (this.selectTerminal[j].id == item.id) {
                    //更新设备信息
                    this.addTerminalIcon(this.beidouTerminal[index]);
                  }
                }
              }
            });
            if (has == false) {
              //如果这个设备是第一次出现终端消息，把这个消息push进去，否则就是以上被替换成新的消息
              this.beidouTerminal.push(this.beidouTerminalMes[0]);
              for (let i = 0; i < this.selectTerminal.length; i++) {
                if (this.selectTerminal[i].id == this.beidouTerminalMes[0].id) {
                  //更新设备信息
                  this.addTerminalIcon(this.beidouTerminalMes[0]);
                }
              }
            }
          }
        }
      }
    },

    //监听跟踪设备改变
    followCarOrGun: {
      handler: function (newer, older) {
        let that = this;
        if (newer || newer == null) {
          this.$store.commit("realTimeMonitor/FOLLOWCARORGUN", newer);
        }
      },
      deep: true
    },
    //监听主页点击跟踪或者是取消跟踪
    "$store.state.realTimeMonitor.followInfo": {
      handler: function (newer, older) {
        let that = this;
        if (newer) {
          this.checkNodes(newer, newer.imei);
        } else if (newer == null) {
          this.followCarOrGun = null;

        }
      },
      deep: true
    },
    //监听主页点击跟踪或者是取消跟踪
    "$store.state.realTimeMonitor.socketTalkData": {
      handler: function (newer, older) {
        this.selectTalkData = { ...newer };
        if (newer.hasOwnProperty('deviceNo')) {
          this.talkReq();
        }
      },
      deep: true
    },

