    //函数功能：初始化地图
    initMap() {
      let that = this;
      this.map = new BMap.Map("map", { enableMapClick: false });
      this.map.centerAndZoom("潮州", 13); //初始化定位到喀什地区
      //添加地图类型控件
      this.map.addControl(
        new BMap.MapTypeControl({
          mapTypes: [BMAP_NORMAL_MAP, BMAP_HYBRID_MAP]
        })
      );

      this.map.enableScrollWheelZoom(true); //开启鼠标滚轮缩放
      //富标注 默认富标注隐藏
      that.myRichMarker = new BMapLib.RichMarker(
        `<div class="myRichMarker">loading<i class="myClose">×</i></div>`,
        new BMap.Point(0, 0),
        {
          // anchor: new BMap.Size(0, 0)
          anchor: new BMap.Size(-400, -334),
          enableDragging: true
        }
      );
      that.myRichMarker.addEventListener("ondragstart", function (e) {
        that.myRichMarkerDraged = true;
      });
      that.map.addOverlay(that.myRichMarker);
      that.myRichMarkerDraged = false;
      that.myRichMarker.hide();
      //初始化信息框偏移，并且监听窗口是否改变并重新设置偏移
      that.mql = window.matchMedia("(max-width: 480px)");
      that.mqCallback(that.mql);
      // 信息框样式设置
      $(".myRichMarker")
        .parent()
        .css({
          "border-radius": "20px",
          background: "transparent"
        });
      // 指定回调函数
      that.mql.addListener(that.mqCallback);
    },
    //函数功能：设置信息框相对与marker偏移位置
    mqCallback(mql) {
      if (mql.matches) {
        // this.myRichMarker.setAnchor(new BMap.Size(-155, 10));
        this.myRichMarker.setAnchor(new BMap.Size(-400, -334));
      } else {
        // this.myRichMarker.setAnchor(new BMap.Size(-360, -310));
      }
    },
