    //车辆勾选事件
    getCheckedNodes(data, node) {
      // 获取被选中的车
      let array = this.$refs.tree.getCheckedNodes(false, false);
      //过滤出车辆  item.type=="device",代表该节点是车，而不是大队和全选
      let filterArray = array.filter(item => {
        return item.type == "device";
      });
      this.selectCarArray = filterArray;

      let optionsArray = []; //车辆设备号
      if (filterArray) {
        filterArray.forEach((item1, index1) => {
          optionsArray.push(item1.id);
        });
        this.elSelectCarArray = optionsArray;
      }
      //data有值代表是勾选操作，data没值的时候是为了要实现勾选操作而执行的这个函数
      if (data) {
        //勾选的车辆
        if (data.type == "device") {
          this.selectAllOrPart = "part";
          this.selectImei = data.id;
          //疑问：问什么勾选的时候只有是在下面就是实时轨迹的时候才执行点击事件？
          //答：由于实时轨迹是只有出现有了推送数据以后才会显示在地图上，因此只会在这个位置执行点击事件，把该设备变成被跟踪设备
          //疑问：那为什么实时位置不是在这个位置执行点击事件？有什么不同吗
          //答：假设在这个位置点击，此时会先执行点击事件，同时此时会请求获取设备最新位置及其他信息
          //但是由于请求还没回来，之前的点击事件里面会由于没有请求回来数据而让信息框不显示造成bug,因此实时位置状态会在请求最新位置的回调里面执行点击事件而不是在这
          if (this.realTimeTrailValue == true) {
            $(".p" + data.id).click();
          }
          //跟踪设备
          this.followCarOrGun = data.id;
          //标记信息窗口显示的是哪个设备的信息
          this.richMarkerImeiIndex = data.id;
          // 勾选的是大队或者是全选
        } else {
          this.selectAllOrPart = "all";
          this.selectImei = null;
        }
      }
    },

