    // 终端设备勾选事件 移除地图上多余的设备 增加新勾选的设备
    getCheckedNodesTerminal(data, node) {
      // 获取被选中的终端
      let array = this.$refs.treeBeidouTerminal.getCheckedNodes(false, false);
      //过滤出终端  item.type=="device",代表该节点是终端，而不是大队和全选
      let filterArray = array.filter(item => {
        return item.id !== "北斗终端";
      });
      this.selectTerminal = filterArray;
      let terminalIconArr = [...this.terminalIcon];
      //找出已存在的marker
      for (let i = 0; i < filterArray.length; i++) {
        let hasIon = false; //地图上是否已经存在这个设备的标志
        for (let k = 0; k < terminalIconArr.length; k++) {
          if (filterArray[i].id == terminalIconArr[k].id) {
            hasIon = true;
          }
        }

        //新增新选择的marker
        if (hasIon == false) {
          let beidouTerminal = [...this.beidouTerminal];
          for (let j = 0; j < beidouTerminal.length; j++) {
            if (filterArray[i].id == beidouTerminal[j].id) {
              if (beidouTerminal[j].lng && beidouTerminal[j].lat) {
                let ggPoint = new BMap.Point(
                  beidouTerminal[j].lng,
                  beidouTerminal[j].lat
                );
                let pointArr = [];
                pointArr.push(ggPoint);
                let convertorData = Gps2Bds(pointArr);
                beidouTerminal[j].Blng = convertorData[0].lng;
                beidouTerminal[j].Blat = convertorData[0].lat;
                let point = new BMap.Point(
                  beidouTerminal[j].Blng,
                  beidouTerminal[j].Blat
                );
                if (filterArray.length == 1) {
                  this.map.panTo(point);
                }
                this.addTerminalIcon(beidouTerminal[j]);
              } else {
                this.$message.warning("没有该终端坐标");
              }
            }
          }
        }
      }
      //去掉取消选择的marker
      for (let i = terminalIconArr.length - 1; i >= 0; i--) {
        let hasIon = false;
        for (let k = 0; k < filterArray.length; k++) {
          if (filterArray[k].id == terminalIconArr[i].id) {
            hasIon = true;
          }
        }
        if (hasIon == false) {
          this.map.removeOverlay(terminalIconArr[i].Icon);

          if (this.richMarkerImeiIndex == terminalIconArr[i].id) {
            this.myRichMarkerDraged = false;
            this.myRichMarker.hide();
          }
          this.terminalIcon.splice(i, 1);
        }
      }
    },
    //左侧树状结构节点单击事件 设置语言功能的设备号和通道等信息
    selectVideoCar(e) {

      //没有绑定设备的车辆提示
