    //播放实时视频函数
    playLiveVideo(index, parentId) {
      // 播放实时视频
      let title = "";
      let videoIndex = index; // index  窗口下标
      let channel = index; // 通道 车上的摄像头的编号
      let stream = 1; // 码流
      let jsession = sessionStorage.getItem("jsession"); // 会话号
      let devIdno = parentId; // 设备号   根据parentId获取设备号或者是车牌号
      let carNumber = this.$store.state.realTimeMonitor.carAndGunObj[parentId]
        .code; // 车牌号   与设备号2选1
      title = carNumber + " - " + "CH" + (index + 1);
      // debugger;
      // let minBufferTime = 2   // 最小缓冲时长
      // let maxBufferTime = 6   // 最大缓冲时长  主要用于调整视频延时，当缓存的数据达到了最小缓冲时长时（默认为2秒），则会进行播放，当到了最大缓冲时长（默认为6秒），则进行快放。
      swfobject.getObjectById(this.divId).setVideoInfo(index, title); // 设置标题
      swfobject.getObjectById(this.divId).stopVideo(videoIndex); // 先将当前窗口下标的视频停止播放


      //播放视频

      swfobject
        .getObjectById(this.divId)
        .startVideo(videoIndex, jsession, devIdno, channel, stream, true);
    },
