    //点击开启监听
    open() {
      if (this.talkInterval) {
        window.clearInterval(this.talkInterval);
        this.talkInterval = null;
      }
      if (this.isVideo == false) {
        this.$message.warning("请切换到视频模式");
        return;
      }
      if (this.talkAndMonitor.imei == null && this.radio == "1") {
        this.$message.error("请单击车辆后进行操作");
        return;
      } else if (
        (this.talkAndMonitor.imei == null || this.talkAndMonitor.chn == null) &&
        this.radio == "2"
      ) {
        this.$message.error("请单击车辆通道后进行操作");
        return;
      }
      this.openState = true;
      // 开启对讲还是监听
      if (this.radio == "1") {
        this.startTalkback(this.talkAndMonitor.imei);
      } else if (this.radio == "2") {
        this.startMonitor(this.talkAndMonitor.imei);
      }
    },
    //点击关闭监听
    stop() {
      if (this.isVideo == false) {
        this.$message.warning("请切换到视频模式");
        return;
      }
      // 停止对讲还是监听
      // 对讲
      if (this.radio == "1") {
        swfobject.getObjectById(this.divId).stopTalkback();
        this.talkhintText = '';
      } else if (this.radio == "2") {
        // 监听
        swfobject.getObjectById(this.divId).stopListen(); // 停止监听
      }

      // 请空数据
      this.openState = false;
    },
    //函数功能：毫秒数转成年月日时分秒格式 如:2020-03-11 16:23:52
    formatDate(timestamp) {
      if (isNaN(timestamp)) {
        return '';
      } else {
        let date = new Date(timestamp);
        let year = date.getFullYear();
        let month = this.addZero(date.getMonth() + 1);
        let day = this.addZero(date.getDate());
        let hours = this.addZero(date.getHours());
        let minutes = this.addZero(date.getMinutes());
        let seconds = this.addZero(date.getSeconds());

        return (
          year +
          "-" +
          month +
          "-" +
          day +
          " " +
          hours +
          ":" +
          minutes +
          ":" +
          seconds
        );
      }
    },
    formatDate1(timestamp) {
      if (isNaN(timestamp)) {
        return null;
      } else {
        let date = new Date(timestamp);
        let year = date.getFullYear();
        let month = this.addZero(date.getMonth() + 1);
        let day = this.addZero(date.getDate());

        return year + "_" + month + "_" + day;
      }
    },
    //个位数补0
    addZero(num) {
      return num < 10 ? "0" + num : num;
    },

    addCarToMap(message, type, alarm) {
      //type: 0：默认离线状态，1：在线，2：gps信号丢失，3：网络异常 4：电量为0 5：fake状态 6:在线无坐标
      //其中1显示为在线 0，2，3显示为离线（以前做了区分，现在不区分，但是为防止修改暂时还是在type上做个区分）
      // 4：显示为电量为0状态图标（目前这个状态不用，用sessionStorage.aloneObj来判断电量为0时改变设备图标） 5显示未fake状态图标
      //注：如果这些状态的设备处于需要显示为红色的报警状态（用sessionStorage.aloneObj来判断是否需要显示未脱离），会层级高于以上的type状态，被显示为报警状态

      //如果是 type为0和1的时候没有坐标则不用添加marker，为无效状态
      if (!message.lng && !message.lat && type == 0) {
        return;
      }
      let that = this;
      let repeatIndex = null; //查找到的相同的imei号的索引
      let img; //显示的图标（在线车辆，离线车辆，在线枪支，离线枪支）
      let pointArr = []; //pointArr[0]:当前点经纬度
      let ggPoint = {}; //当前坐标点
      let convertorData = []; //转为百度坐标以后的坐标数组
      let Blng = null; //百度经度
      let Blat = null;
      let Icon = null; //被添加设备marker对象
      let point = {}; //被添加设备点
      let aloneObj1 = JSON.parse(sessionStorage.aloneObj); //脱离等需要把设备颜色变为闪烁红的设备对象
      for (let index = 0; index < that.makerIconArr.length; index++) {
        if (that.makerIconArr[index].imei == message.imei) {
          repeatIndex = index;
          break;
        }
      }
      //如果该设备现在没有添加在地图上，而且没有位置坐标的话也为无效状态
      if (repeatIndex == null && (!message.lng || !message.lat)) {
        return;
      }
      //判断该设备是否处于红色的报警状态,如果是的话就改变设备图标，不是的话就按type来判断改变设备图标
      if (aloneObj1.hasOwnProperty(message.imei)) {
        if (aloneObj1[message.imei] !== 10007) {
          if (message.type == 2) {
            img = aloneCar;
          } else if (message.type == 1) {
            img = aloneGun;
          }
        } else {
          if (message.type == 2) {
            img = gpsCar; //电量为0时显示的图标
          } else if (message.type == 1) {
            img = gpsGun;
          }
        }
      } else {
        if (type == 0 || type == 2) {
          if (message.type == 2) {
            img = offlineCar;
          } else {
            img = offlineGun;
          }
        } else if (type == 3) {
          if (message.type == 2) {
            img = gpsCar2;
          } else {
            img = gpsGun2;
          }
        } else if (type == 1) {
          if (message.type == 2) {
            img = onlineCar;
          } else {
            img = onlineGun;
          }
        } else if (type == 4) {
          if (message.type == 2) {
            img = gpsCar;
          } else {
            img = gpsGun;
          }
        } else if (type == 5) {
          if (message.type == 2) {
            img = fakecar;
          } else {
            img = fakegun;
          }
        } else if (type == 6) {
          if (message.type == 2) {
            img = noPositionCar;
          } else {
            img = noPositionGun;
          }
        }
      }
      // console.warn(message);
      //   console.warn('status',type);
      // console.warn('speed',message.speed);
      // console.warn('lng',message.lng);
      //  console.warn('img1',img);
      if ((img == onlineCar || img == fakecar) && that.speedObj[message.imei] == 1) {
        img = speedCar;
      }
      if ((img == onlineGun || img == fakegun) && that.speedObj[message.imei] == 1) {
        img = speedGun;
      }

      // console.warn('img2',img);


      if (repeatIndex !== null && alarm == 'alarm') {
        //报警或者取消报警改变颜色
        let marker11 = new BMap.Icon(img, this.bmapSize, {
          imageSize: this.bmapSize
        });
        that.makerIconArr[repeatIndex].Icon.setIcon(marker11);

        if (message.type == 2) {
          that.makerIconArr[repeatIndex].Icon.setRotation(that.makerIconArr[repeatIndex].Icon.getRotation());

          // that.makerIconArr[repeatIndex].IconLabel.setRotation(that.makerIconArr[repeatIndex].IconLabel.getRotation());

        }
        return;
      }
      //如果已经在地图上添加过这个设备，但是经度或纬度值没有，则还用以前的经纬度，否则用最新的经纬度
      if (!message.lng && !message.lat && repeatIndex !== null) {
        //如果当前点的经纬度不存在或者都是0，并且存在repeatIndex
        ggPoint = new BMap.Point(
          that.makerIconArr[repeatIndex].lng,
          that.makerIconArr[repeatIndex].lat
        );
      } else if (message.lng && message.lat) {
        ggPoint = new BMap.Point(message.lng, message.lat);
      }
      //原始坐标转百度坐标
      pointArr.push(ggPoint);
      convertorData = Gps2Bds(pointArr);
      if (convertorData.length !== 0) {
        Blng = convertorData[0].lng;
        Blat = convertorData[0].lat;

        point = new BMap.Point(Blng, Blat);
        if (!this.rayCasting(Blat, Blng, this.CreateChinaMapLine())) {
          return;
        }
        //    //
        //    let myGeo = new BMap.Geocoder(); // 根据坐标得到地址描述

        // myGeo.getLocation(point, function(result) {
        //   if(result){
        //     // console.warn(result.addressComponents.province)
        //   }
        // })
        //   //
        let marker = new BMap.Icon(img, this.bmapSize, {
          imageSize: this.bmapSize
        });

        let lng = message.lng;
        let lat = message.lat;
        let direction = 0;
        if (message.direction !== undefined && message.direction !== null) {
          direction = message.direction + 90;
        } else {
          direction = 90;
        }
        //已经在地图上添加过的设备改变设备图标
        if (repeatIndex !== null) {
          //这几个状态都是没有经纬度值的，还是使用原来的已经渲染在地图上的经纬度值
          if (type == 2 || type == 3 || type == 4||type==6) {
            lng = that.makerIconArr[repeatIndex].lng;
            lat = that.makerIconArr[repeatIndex].lat;
          }
          else {
            lng = message.lng;
            lat = message.lat;
          }
          //值几个状态都是有经纬度值的，需要重新改变图标在地图上的位置
          if (type == 0 || type == 1 || type == 5) {
            that.makerIconArr[repeatIndex].Icon.setPosition(point);
            // that.makerIconArr[repeatIndex].IconLabel.setPosition(point);

          }
          //重新setIcon
          that.makerIconArr[repeatIndex].Icon.setIcon(marker);

          //只有车辆才需要改变旋转角度，枪支不需要
          if (message.type == 2) {
            // console.warn(that.makerIconArr[repeatIndex])
            that.makerIconArr[repeatIndex].Icon.setRotation(direction); // 旋转的角度
            // that.makerIconArr[repeatIndex].IconLabel.setRotation(direction);
          }
          //大队信息，车牌或者是枪号信息
          let teamName = that.makerIconArr[repeatIndex].teamName;
          let licenseNo = that.makerIconArr[repeatIndex].licenseNo;
          //显示在地图信息弹窗的信息需要更新
          that.makerIconArr[repeatIndex].lng = message.lng;
          that.makerIconArr[repeatIndex].lat = message.lat;
          that.makerIconArr[repeatIndex].speed = message.speed;
          // console.warn('speed1', that.makerIconArr[repeatIndex].speed);
          //如果枪支没有上传电量，就不改变原来的电量值
          if (message.battery !== null) {
            that.makerIconArr[repeatIndex].battery = message.battery;
          }
          that.makerIconArr[repeatIndex].direction = message.direction;
          that.makerIconArr[repeatIndex].time = message.time;

          that.makerIconArr[repeatIndex].IconType = type;
          that.makerIconArr[repeatIndex].onlineType = type;
          that.makerIconArr[repeatIndex].Blng = Blng;
          that.makerIconArr[repeatIndex].Blat = Blat;
          that.makerIconArr[repeatIndex].teamName = teamName;
          that.makerIconArr[repeatIndex].licenseNo = licenseNo;
          //这个是鼠标放上去要显示的所有设备位置
          that.makerIconArr[repeatIndex].p = that.map.pointToPixel(
            that.makerIconArr[repeatIndex].Icon.point
          );
          that.makerIconArr[repeatIndex].number = message.number;
          that.makerIconArr[repeatIndex].rfids = message.rfids;
          that.makerIconArr[repeatIndex].reduceNumber = message.reduceNumber;
          that.makerIconArr[repeatIndex].reduceRfids = message.reduceRfids;
          that.makerIconArr[repeatIndex].addNumber = message.addNumber;
          that.makerIconArr[repeatIndex].addRfids = message.addRfids;


          if (!message.lng || !message.lat) {
            that.makerIconArr[repeatIndex].lng = lng;
            that.makerIconArr[repeatIndex].lat = lat;
          }
          //显示的状态信息判断标志
          that.makerIconArr[repeatIndex].exType = message.exType;
          // 富标注实时定位 （改变富标注里面显示的内容）
          if (message.imei === this.richMarkerImeiIndex) {
            that.openInfo(
              that.makerIconArr[repeatIndex],
              "changeRichMarkerInfo"
            );
          }
          //报警的设备层级高与非报警设备
          if (img == aloneGun || img == aloneCar) {
            that.makerIconArr[repeatIndex].Icon.setZIndex(2);
          } else {
            that.makerIconArr[repeatIndex].Icon.setZIndex(1);
          }
        } else {
          if (!message.lng && !message.lat) {
            return;
          }
          //第一次添加设备初始化信息添加设备marker，其中如果是从实时轨迹切换到实时位置(标志为'trailToPos')（图标还使用原来实时轨迹的图标，因此这个地方不再添加图标，Icon也会被赋值为实时轨迹的Icon），
          let teamName = "";
          let licenseNo = "";
          if (this.carAndGunObj.hasOwnProperty(message.imei)) {
            teamName = this.carAndGunObj[message.imei].teamName;
            licenseNo = this.carAndGunObj[message.imei].code;
          }

          Icon = new BMap.Marker(point, {
            icon: marker,
            title: message.licenseNo
          });
          if (this.carMkObj.hasOwnProperty(message.imei)) {
            // delete this.carMkObj[message.imei].message;
            Icon = this.carMkObj[message.imei];
          }


          message.teamName = teamName;
          message.licenseNo = licenseNo;
          message.Icon = Icon;
          message.onlineType = type;
          message.IconType = type;
          message.p = that.map.pointToPixel(Icon.point);
          message.Blng = Blng;
          message.Blat = Blat;
          message.Icon.setLabel(this.setLabelStyle(licenseNo));
          
          // message.IconLabel=new BMap.Label(licenseNo,{position : Icon.point, offset: new BMap.Size(0,0)});

          let a = { ...message };

          that.makerIconArr.push(a);
          that.addClickHandler(message, Icon, "carOrGun");
          if (!this.carMkObj.hasOwnProperty(message.imei)) {
            that.map.addOverlay(Icon);
            // that.map.addOverlay( message.IconLabel);
          }

          if (img == aloneGun || img == aloneCar) {
            message.Icon.setZIndex(2);
          } else {
            message.Icon.setZIndex(1);
          }
          if (message.type == 2) {
            that.makerIconArr[that.makerIconArr.length - 1].Icon.setRotation(
              direction
            ); // 旋转的角度
            //  that.makerIconArr[that.makerIconArr.length - 1].IconLabel.setRotation(
            //   direction
            // ); // 旋转的角度

          }
        }
        //跟踪车辆或枪支
        if (message.imei == this.followCarOrGun) {
          this.bound = this.map.getBounds();
          if (!that.bound.containsPoint(point)) {
            this.map.panTo(point);
          }
        }

      }
    },
    setLabelStyle(content) {
      //左偏移  右偏移
      let offsetSize = new BMap.Size(0, 0);
      let labelStyle = {
        color: "black",
        backgroundColor: "white",
        borderColor: "black",
        // border: "0",
        // opacity:"0.8",
        verticalAlign: "center",
        // borderRadius: "2px",
        // whiteSpace:"normal",
        // wordWrap:"break-word",
        // padding:"7px",
      };
      //用于设置样式
      var spanA = "<span class='angle'><span>"
      offsetSize = new BMap.Size(-5, -22);

      // //不同数字长度需要设置不同的样式。
      // var num=parseInt(content.length/10)
      // switch(num) {
      //     case 0:
      //         offsetSize = new BMap.Size(-20, -40);
      //         break;
      //     case 1:
      //         offsetSize = new BMap.Size(-20, -40);
      //         break;
      //     case 2:
      //         offsetSize = new BMap.Size(-20, -60);
      //         break;
      //     case 3:
      //         offsetSize = new BMap.Size(-20, -80);
      //         break;
      //     default:
      //         break;
      // }

      let label = new BMap.Label(content + spanA, {
        offset: offsetSize
      });
      label.setStyle(labelStyle);
      return label;
    },
    //图标重合
    addMouseover(message) {
      let t = this,
        map = t.map;
      let title = ``;
      t.makerIconArr.forEach((a, j) => {
        a.p = map.pointToPixel(a.Icon.point);
      });
      t.makerIconArr.forEach((a, j) => {
        if (
          Math.abs(a.p.x - message.p.x) < 13 &&
          Math.abs(a.p.y - message.p.y) < 13
        ) {
          // 视为重合
          title += `${a.licenseNo}\n`;
        }
      });
      message.Icon.setTitle(title);
    },

    //改变信息窗口显示内容
    //content：信息窗口显示内容 e：点击事件 type:用来区分是添加点击事件执行的这个函数还是需要改变信息窗口内容执行的这个函数
    //type == "changeRichMarkerInfo"代表是需要改变实时位置信息窗口内容 type == "changerRealTrail"代表是需要改变实时轨迹信息窗口内容 （由于两个point位置信息需要从不同方式获取）
    openInfo(content, type) {
      let that = this;
      let point = null;
      this.richMarkerImeiIndex = content.imei; //信息窗口标志
      let myGeo = null; // 根据坐标得到地址描述
      let address = ""; //地址
      let direction = null; //方向
      let lng = null;
      let lat = null;
      let speed = null; //速度
      let battery = null;
      let exception = null; //状态
      let teamName = ""; //所属大队
      let licenseNo = ""; //枪号或者车牌号

      //实时位置和实时轨迹 两个point位置信息需要从不同方式获取，实时轨迹没有报错Blng和Blat
      if (type == "changeRichMarkerInfo") {
        point = new BMap.Point(content.Blng, content.Blat);
      } else if (type == "changerRealTrail") {
        if (that.carMkObj[content.imei]) {
          point = new BMap.Point(
            that.carMkObj[content.imei].point.lng,
            that.carMkObj[content.imei].point.lat
          );
        }
      }

      myGeo = new BMap.Geocoder(); // 根据坐标得到地址描述
      direction = that.formateDirection(content.direction);
      lng = this.formateLngLatSpeed(content.lng);
      lat = this.formateLngLatSpeed(content.lat);
      speed = this.formateLngLatSpeed(content.speed);
      battery = this.formateLngLatSpeed(content.battery);
      exception = this.formateException(content.exType);
      if (this.carAndGunObj.hasOwnProperty(content.imei)) {
        teamName = this.carAndGunObj[content.imei].teamName;
        licenseNo = this.carAndGunObj[content.imei].code;
      }
      if (speed !== "") {
        if (content.type == 1) {
          speed = (speed * 1.852).toFixed(3) + " km/h";
        } else {
          speed = speed.toFixed(3) + " km/h";
        }
      }
      // //以下为曾经放回调代码
      // if (content.address) {
      //   address = content.address;
      // } else {
      //   address = result.address;
      // }
      // console.warn(result.addressComponents)
      //绑定显示
      let [bindDeviceTitle] = that.formateBindDevice(
        content.imei,
        content.type
      );
      let bindHtml = '';

      if (that.bindCarandgun[content.imei]) {
        for (let i = 0; i < that.bindCarandgun[content.imei].length; i++) {

          bindHtml += `<span class="bindDev mesContentCli">${that.bindCarandgun[content.imei][i]
            ? that.bindCarandgun[content.imei][i]
            : "暂无"
            }</span>`
        }
      }
     


      let html = `  <div class="mesBox">
                    <div class="mesTitle">${licenseNo}</div>
                    <div class="mesBody">
                      <div class="mesItem mesItemAddress">
                        <div class="mesLabel ">经度</div>
                        <div class="mesContent" >${lng}</div>
                        
                      </div>
                       <div class="mesItem mesItemAddress">
                        <div class="mesLabel ">纬度</div>
                        <div class="mesContent" >${lat}</div>
                        
                      </div>
                       <div class="mesItem mesItemAddress none">
                        <div class="mesLabel ">款箱数量</div>
                        <div class="mesContent mesContentCli boxNum" ><span class="boxTotalNum">${content.number==undefined?'':(content.number)}   </span></div>
                      </div>
                      <div class="mesItem">
                        <div class="mesLabel">设备号</div>
                        <div class="mesContent">${content.imei}</div>
                      </div>  
                      <div class="mesItem">
                        <div class="mesLabel">状态</div>
                        <div class="mesContent">${exception}</div>
                      </div>
                      <div class="mesItem">
                        <div class="mesLabel">时间</div>
                        <div class="mesContent">${that.formatDate(
        parseInt(content.time)
      )}</div>
                      </div>
                      <div class="mesItem">
                        <div class="mesLabel">速度</div>
                        <div class="mesContent">${speed}</div>
                      </div>
                    
                       <div class="mesItem">
                        <div class="mesLabel bind">${bindDeviceTitle}</div>
                       <i class="el-icon-refresh mesContentCli"></i>

                        <div class="mesContent mesContent-dev ">${bindHtml}</div>
                      </div>
                      <div class="mesItem mesItemAdrress">
                       <div class="mesContent mesContent-address "><span class="addressContent">${that.addressObj.address}</span><span title="复制"  class="el-icon-document-copy mesContentCli  copySpan"></span><span title="添加网点"  class="el-icon-circle-plus-outline mesContentCli  addSpan"></span></div>
                      </div>
                      
                    </div>
                  </div>`;

                  // <div class="mesContent mesContentCli boxNum" ><span class="boxTotalNum">${content.number==undefined?'':content.number}</span> <span class="el-icon-plus boxAddNum ${content.addNumber == null ? "none" : ""}">${content.addNumber==undefined?'':content.addNumber}</span><span class="el-icon-minus boxReduceNum ${content.reduceNumber == null ? "none" : ""}">${content.reduceNumber==undefined?'':content.reduceNumber}</span></div>

      // 设置最新位置
      if (that.myRichMarkerDraged == false) {
        that.myRichMarker.setPosition(point);
      }
      that.myRichMarker.setContent(
        `<div class="myRichMarker">${html}<i class="myClose">×</i></div>`
      );
      that.myRichMarker.show();
      if (new Date() - that.addressObj.lastTime > 10000 || that.addressObj.lastTime == null) {
        that.addressObj.lastTime = new Date();
        if (lng && lat) {
          that.addressObj.address = '';
          that.handleAddress(lng, lat,content.imei).then((res) => {
            that.addressObj.address = res;
            $('.addressContent').text(res);
          })
        }
      }
      // let addressEle = document.querySelector(".mesItemAddress");
      // addressEle.onclick = function () {
      //   if (lng && lat) {
      //     that.handleAddress(lng,lat).then((res) => {
      //        that.addressObj.address=res;
      //       $('.mesContent-address').text(res);
      //     })
      //   }
      // }
      //点击调转车辆款箱详情界面（当前车辆所有款箱）
      let boxNumEle = document.querySelector(".boxTotalNum");
      boxNumEle.onclick = function () {
       if(content.number){
        that.boxDialogVisible=true;
         that.boxList=[]
          that.boxLstLoading=true;
          let rfids=content.rfids!=undefined?content.rfids.split(','):'';
          pacQueryByRfids(rfids).then(res => {
          that.boxList=Object.keys(res.data).map(function(i){return res.data[i]});
        }).finally(() => {
          that.boxLstLoading = false;
        });
       }
      }
      //  //点击调转车辆款箱详情界面(增加款箱)
      // let boxAddNumEle = document.querySelector(".boxAddNum");
      // boxAddNumEle.onclick = function () {
      //  if(content.addNumber){
      //   that.boxDialogVisible=true;
      //    that.boxList=[]
      //     that.boxLstLoading=true;
      //     let rfids=content.addRfids.split(',');
      //     pacQueryByRfids(rfids).then(res => {
      //     that.boxList=Object.keys(res.data).map(function(i){return res.data[i]});
      //   }).finally(() => {
      //     that.boxLstLoading = false;
      //   });
      //  }
      // } 
      //  //点击调转车辆款箱详情界面（减少的款箱）
      // let boxReduceNumEle = document.querySelector(".boxReduceNum");
      // boxReduceNumEle.onclick = function () {
      //  if(content.reduceNumber){
      //   that.boxDialogVisible=true;
      //    that.boxList=[]
      //     that.boxLstLoading=true;
      //     let rfids=content.reduceRfids.split(',');
      //     pacQueryByRfids(rfids).then(res => {
      //     that.boxList=Object.keys(res.data).map(function(i){return res.data[i]});
      //   }).finally(() => {
      //     that.boxLstLoading = false;
      //   });
      //  }
      // }
      //复制地址功能
      let copyEle = document.querySelector(".copySpan");
      copyEle.onclick = function () {
        let _input = document.createElement("input");   // 直接构建input
        _input.value = that.addressObj.address;  // 设置内容
        document.body.appendChild(_input);    // 添加临时实例
        _input.select();   // 选择实例内容
        document.execCommand("Copy");   // 执行复制
        document.body.removeChild(_input); // 删除临时实例
      }
      //点击添加网点功能
      let addEle = document.querySelector(".addSpan");
      addEle.onclick = function () {
        that.addNetworkVisible = true;
        that.addNetworkTemp.lng = content.Blng;
        that.addNetworkTemp.lat = content.Blat;
        that.addNetworkTemp.networkSearchVal = that.addressObj.address;

      }
      if (!that.bindRefreshEle) {
        that.bindRefreshEle = document.querySelector(".el-icon-refresh");
        that.bindRefreshEle.onclick = function () {
          that.bindCarandgun = {};
          Promise.all(that.getbindCarOrGun()).then(() => {
            let bindHtml = '';

            if (that.bindCarandgun[content.imei]) {
              for (let i = 0; i < that.bindCarandgun[content.imei].length; i++) {
                bindHtml += `<span class="bindDev mesContentCli">${that.bindCarandgun[content.imei][i]
                  ? that.bindCarandgun[content.imei][i]
                  : "暂无"
                  }</span>`
              }
            }
            $('.mesContent-dev').html(bindHtml);

          }).catch((error) => {

          })

        }
      }
      // addressEle.addEventListener("click", function () {
      //   if (lng && lat) {
      //     that.handleAddress(point).then((res) => {
      //       $('.mesContent-address').text(res);
      //     })
      //   }
      // })
      let bindDevEle = document.querySelector(".mesContent-dev");
      // console.warn(bindDevEle)
      // bindDevEle.addEventListener("click", function (e) {
      //   console.warn(that.carAndGunObj);
      //   for (let i in that.carAndGunObj) {
      //     if (that.carAndGunObj[i].code == e.target.innerText) {
      //       that.checkNodes(content.type == 2 ? 1 : 2, i, true);
      //     }
      //   }
      // })
      bindDevEle.onclick = function (e) {
