    //显示状态
    formateException(type) {
      let exception = "在线";
      if (type == 0) {
        exception = "离线";
      } else if (type == 1) {
        // exception = "网络异常";
        exception = "在线无定位";

      } else if (type == 3) {
        exception = "电量为0";
      } else if (type == 6) {
        exception = "无定位";
      }
      return exception;
    },
    //显示方向
    formateDirection(direction) {
      let directionText = "";
      if (direction == undefined || direction == null) {
        return "";
      }
      if (direction == 0 || direction == 360) {
        directionText = "北";
      } else if (direction < 90) {
        directionText = "东北";
      } else if (direction == 90) {
        directionText = "东";
      } else if (direction < 180) {
        directionText = "东南";
      } else if (direction == 180) {
        directionText = "南";
      } else if (direction < 270) {
        directionText = "西南";
      } else if (direction == 270) {
        directionText = "西";
      } else if (direction < 360) {
        directionText = "西北";
      }

      return directionText + "," + direction + "°";
    },
    //显示经纬度
    formateLngLatSpeed(data, type) {
      if (data == undefined || data == null) {
        return "";
      } else {
        return data;
      }
    },
    //给marker添加点击事件
    //content:添加内容信息 marker1:添加的marker type 'carOrGun':给实时位置添加 'addMarkerEnd':给实时轨迹设备添加
    addClickHandler(content, marker1, type) {
      let that = this;
      let message = null;
      //给实时位置和实时轨迹添加所使用的获取内容方式不一样
      //由于添加点击事件以后位置信息等内容会一直改变，因此不能直接使用content的内容
      //应该从that.makerIconArr（实时位置）或者是that.carMkObj（实时轨迹）中获取动态的内容
      if (type == "carOrGun") {
        //实时推送的时候
        for (let i = 0; i < this.makerIconArr.length; i++) {
          if (content.imei == that.makerIconArr[i].imei) {
            message = that.makerIconArr[i];
            break;
          }
        }
        //图标重合
        marker1.addEventListener("mouseover", function (e) {
          that.addMouseover(message);
        });
        marker1.addEventListener("click", function (e) {
          that.addressObj.lastTime = null;
          that.addressObj.address = '';

          that.openInfo(message, "changeRichMarkerInfo");
          that.myRichMarker.setAnchor(new BMap.Size(-400, -334));
          that.myRichMarkerDraged = false;
          marker1 = null;
        });
      } else if (type == "addMarkerEnd") {
        marker1.addEventListener("click", function (e) {
          that.addressObj.lastTime = null;
          that.addressObj.address = '';


          if (that.carMkObj.hasOwnProperty(content.imei)) {
            message = that.carMkObj[content.imei].message;
          }
          that.openInfo(message, "changeRichMarkerInfo");
          that.myRichMarker.setAnchor(new BMap.Size(-400, -334));
          that.myRichMarkerDraged = false;
        });
      }
      marker1 = null;
    },
    //给终端添加点击事件
    addClickHandlerTerminal(content, marker) {
      let that = this;
      let content1 = null;

      marker.addEventListener("click", function (e) {
        let data = that.beidouTerminal;
        for (let i = 0; i < data.length; i++) {
          if (content.id == data[i].id) {
            content1 = data[i];
            let ggPoint = new BMap.Point(content1.lng, content1.lat);
            let pointArr = [];
            pointArr.push(ggPoint);
            let convertorData = Gps2Bds(pointArr);
            content1.Blng = convertorData[0].lng;
            content1.Blat = convertorData[0].lat;
            break;
          }
        }
        that.openInfoTerminal(content1);
        that.myRichMarker.setAnchor(new BMap.Size(-400, -334));
        that.myRichMarkerDraged = false;
      });

      marker = null;
    },
    //获取车辆对应的大队和车牌或这是枪号信息
    findOtherCarOrGunMes(type, serialNo) {
      let list = null;
      let otherMes = {};
      if (type == 2) {
        list = this.data;
      } else if (type == 1) {
        list = this.gunData;
      }
      for (let i = 0; i < list.length; i++) {
        //公司
        for (let j = 0; j < list[i].children.length; j++) {
          //大队
          for (let k = 0; k < list[i].children[j].children.length; k++) {
            if (list[i].children[j].children[k].id == serialNo) {
              otherMes.licenseNo = list[i].children[j].children[k].label;
              otherMes.teamName = list[i].children[j].teamName1;
              return otherMes;
            }
          }
        }
      }
    },
    //获取左侧大队和车辆tree将数据格式化
    formatData(res, type) {
      let that = this;
      let teamList = [];
      let totalNum = 0;

      //形成大队
      //name属性已经被指定为用于显示的属性（在defaultProps里面）
      //teamName1:用于显示的大队名称 isOnlineNum：大队的在线数量
      for (let item in res.data) {
        if (item == "报废车辆") { continue }
        let obj = {};
        obj.id = "team" + res.data[item][0].id;
        if (res.data[item][0].teamName == "") {
          obj.label = res.data[item][0].teamName;
          obj.teamName1 = "未分组";
          obj.isOnlineNum = 0;
          obj.name =
            "未分组" +
            "(" +
            obj.isOnlineNum +
            "/" +
            res.data[item].length +
            ")";
        } else {
          obj.label = res.data[item][0].teamName;
          obj.teamName1 = res.data[item][0].teamName;
          obj.isOnlineNum = 0;
          obj.name =
            res.data[item][0].teamName +
            "(" +
            obj.isOnlineNum +
            "/" +
            res.data[item].length +
            ")";
        }

        teamList.push(obj);
        this.defaultSelect.push(obj.id);//默认展开大队
      }
      for (let i = 0; i < teamList.length; i++) {
        teamList[i].children = [];
        //大队里面添加车
        //name:用于显示的车牌号 devno：设备号 type:用于勾选时确认是否是勾选的车辆
        for (let j = 0; j < res.data[teamList[i].label].length; j++) {
          totalNum++;
          let obj1 = {};
          let nodevId = res.data[teamList[i].label][j].serialNo;
          //设备号不存在的情况下的特殊id(用于区分)
          if (nodevId == "") { nodevId = 'e' + res.data[teamList[i].label][j].id };
          obj1.id = nodevId;
          obj1.devno = res.data[teamList[i].label][j].serialNo;
          obj1.name = res.data[teamList[i].label][j].licenseNo;
          obj1.label = res.data[teamList[i].label][j].licenseNo;
          obj1.dType = 2;
          obj1.disabled = obj1.devno ? false : true
          // if (res.data[teamList[i].label][j].runType == 1) {
          //   obj1.className = "iconfont iconche1";
          // } else if (res.data[teamList[i].label][j].runType == 0) {
          //   obj1.className = "iconfont iconche1";
          // }
          obj1.type = "device";
          obj1.children = [];
          teamList[i].children.push(obj1);

          //车辆下面添加摄像头
          //type属性用于确定这个节点是否是摄像头节点
          if (
            type == 2 &&
            res.data[teamList[i].label][j].channelNumber !== -1 &&
            res.data[teamList[i].label][j].channelNumber !== 0
          ) {
            for (
              let k = 0;
              k < res.data[teamList[i].label][j].channelNumber;
              k++
            ) {
              let obj2 = {
                id: k + "s" + nodevId,
                name: "CH" + (k + 1),
                label: "CH" + (k + 1),
                type: "camera",
                index: k,
                isHiddenSelect: true,
                parentId: nodevId,
                licenseNo: res.data[teamList[i].label][j].licenseNo,
                total: res.data[teamList[i].label][j].channelNumber
              };
              teamList[i].children[j].children.push(obj2);

            }
          }
        }
        this.getSortByCarNo(teamList[i].children)
      }
      //按大队内车辆数量对大队排序
      teamList.sort(function (a, b) {
        return b.children.length - a.children.length;
      });
      //在按车辆数量排队的基础上再按this.sortedTeam里面指定的大队拍在大队最前面
      teamList = this.teamSort(teamList, this.sortedTeam);
      //形成树状结构
      that.data = [
        {
          id: "center1",
          name: this.company + "(车" + "0/" + totalNum + ")",
          label: this.company,
          children: teamList,
          inlineNum: 0,
          totalNum: totalNum
        }
      ];
    },
    getNum(Str) {

      let arr = Str.match(/\d{1,}/g);
      if (arr.length && Str.indexOf[arr[arr.length - 1].slice(-1)] !== -1) {
        return arr[arr.length - 1];
      } else {
        return 0;
      }

    },
    //按车牌号从小到大排序
    getSortByCarNo(arr) {
      for (let j = 0; j < arr.length - 1; j++) {

        for (let i = 0; i < arr.length - 1 - j; i++) {
          let no1 = 0, no2 = 0;
          //获取字符串的最后几位连续数字，如果没有数字则默认为0，后面按数字从小到大排序
          no1 = this.getNum(arr[i].label);
          no2 = this.getNum(arr[i + 1].label);
          if (Number(no1) > Number(no2)) {
            let temp = arr[i];
            arr[i] = arr[i + 1];
            arr[i + 1] = temp;
          }
        }
      }
      return arr;
    },
    //大队按指定大队顺序排序
    teamSort(list, cur) {
      let arr = list;
      let push = [];
      let otherTeam = [];
      for (let x in arr) {
        if (this.inArray(arr[x].label, cur)) {
          arr[x]["sort"] = cur.indexOf(arr[x].label);
          push.push(arr[x]);
        } else {
          otherTeam.push(arr[x]);
        }
      }
      push = push.sort(function (a, b) {
        if (a.sort < b.sort) {
          return -1;
        } else if (a.sort > b.sort) {
          return 1;
        }
        return 0;
      });
      return push.concat(otherTeam);
    },
    //对枪支优先按枪号进行排序，其余的排在最后
    gunSort(arrObj, keyName, type) {
      //这里如果 直接等于arrObj，相当于只是对对象的引用，改变排序会同时影响原有对象的排序，而通过arrObj.slice(0)，
      //表示把对象复制给另一个对象，两者间互不影响
      let tempArrObj = arrObj.slice(0);
      let compare = function (keyName, type) {
        return function (obj1, obj2) {
          let val1 = obj1[keyName].split("*")[1];
          let val2 = obj2[keyName].split("*")[1];
          if (!isNaN(Number(val1)) && !isNaN(Number(val2))) {
            val1 = Number(val1);
            val2 = Number(val2);
          }
          //如果值为空的，放在最后
          if (val1 == null && val2 == null) {
            return 0;
          } else if (val1 == null && val2 != null) {
            return type == 1 ? -1 : 1;
          } else if (val2 == null && val1 != null) {
            return type == 1 ? 1 : -1;
          }
          //排序
          if (val1 < val2) {
            return type == 1 ? 1 : -1;
          } else if (val1 > val2) {
            return type == 1 ? -1 : 1;
          } else {
            return 0;
          }
        };
      };
      return tempArrObj.sort(compare(keyName, type));
    },
    //判断是否在数组里面
    inArray(search, array) {
      for (let i in array) {
        if (array[i] == search) {
          return true;
        }
      }
      return false;
    },
    //形成枪支树状结构
    formatGunData(res, type) {
      let that = this;

      let teamList = [];

      let totalNum = 0;
      //大队
      //大队里面添加枪支
      //name:用于显示的枪号 devno：设备号 type:用于勾选时确认是否是勾选的枪支
      for (let item in res.data) {
        if (item == "报废枪支") { continue }
        let obj = {};
        obj.id = "team" + res.data[item][0].id;
        obj.name = res.data[item][0].teamName;
        if (res.data[item][0].teamName == "") {
          obj.label = res.data[item][0].teamName;
          obj.teamName1 = "未分组";
          obj.isOnlineNum = 0;
          obj.name =
            "未分组" +
            "(" +
            obj.isOnlineNum +
            "/" +
            res.data[item].length +
            ")";
        } else {
          obj.label = res.data[item][0].teamName;
          obj.teamName1 = res.data[item][0].teamName;
          obj.isOnlineNum = 0;
          obj.name =
            res.data[item][0].teamName +
            "(" +
            obj.isOnlineNum +
            "/" +
            res.data[item].length +
            ")";
        }

        teamList.push(obj);
        this.defaultSelectGun.push(obj.id);//默认展开大队

      }
      //大队里面添加对应的枪支
      for (let i = 0; i < teamList.length; i++) {
        teamList[i].children = [];
        //枪
        for (let j = 0; j < res.data[teamList[i].label].length; j++) {
          totalNum++;
          let obj1 = {};
          obj1.id = res.data[teamList[i].label][j].iMei;
          obj1.devno = res.data[teamList[i].label][j].iMei;
          obj1.name = res.data[teamList[i].label][j].code;
          obj1.label = res.data[teamList[i].label][j].code;
          obj1.dType = 1;
          // obj1.className = "iconfont iconqiangzhi";

          obj1.type = "device";
          obj1.children = [];
          teamList[i].children.push(obj1);
        }
      }
      //先按大队里面对应的枪支数量对大队排序
      teamList.sort(function (a, b) {
        return b.children.length - a.children.length;
      });
      //在这基础上按需要 把this.sortedTeam里面的大队按顺序排在最前面
      teamList = this.teamSort(teamList, this.sortedTeam);
      //对每个大队下面的枪支按枪支序号排序，剩余的默认排在最后
      for (let ii = 0; ii < teamList.length; ii++) {
        teamList[ii].children = this.gunSort(teamList[ii].children, "name", -1);
      }
      //形成枪支树状结构数据
      that.gunData = [
        {
          id: "center1",
          name: this.company + "(枪" + "0/" + totalNum + ")",
          label: this.company,
          children: teamList,
          inlineNum: 0,
          totalNum: totalNum
        }
      ];
    },

    //函数功能：获取左侧大队和车辆和枪支tree请求，并形成树状结构所需数据结构
    getTeamAndCarList() {
      let that = this;
      let p1 = new Promise((resolve, reject) => {
        teamAndCarAllBindList({}).then(res => {

          if (res.status == 0) {
            this.allCarCode = []
            for (let item in res.data) {
              for (let i = 0; i < res.data[item].length; i++) {
                if(res.data[item][i].serialNo){
                this.allCar.push(res.data[item][i].serialNo);
                this.allCarCode.push(res.data[item][i].licenseNo)
                } 
              }
            }
            this.getLastGps(2);

            that.formatData(res, 2);
            resolve(this.allCarCode);
          } else {
            this.$message.error(res.msg);
          }
        });
      })
      let p2 = new Promise((resolve, reject) => {
        teamAndGunList({}).then(res => {
          if (res.status == 0) {
            this.allGunCode = []
            for (let item in res.data) {
              for (let i = 0; i < res.data[item].length; i++) {
                if(res.data[item][i].iMei){
                this.allGun.push(res.data[item][i].iMei);
                this.allGunCode.push(res.data[item][i].code);
                }

              }
            }
            this.getLastGps(1);

            that.formatGunData(res, 1);
            resolve(this.allGunCode);
            // //跟踪(必须要有延时，否则不会被勾选成功)
            // setTimeout(() => {
            //   if (this.$store.state.realTimeMonitor.followInfo) {
            //     this.checkNodes(
            //       this.$store.state.realTimeMonitor.followInfo,
            //       this.$store.state.realTimeMonitor.followInfo.imei,true
            //     );
            //   }
            // }, 2000);
          } else {
            this.$message.error(res.msg);
          }
        });
      })
      // 跟踪
      Promise.all([p1, p2]).then(res => {
        this.bindCarandgun = {};
        this.getbindCarOrGun();

        let type;
        if (this.allCar.indexOf(this.$store.state.realTimeMonitor.followCarOrGun) !== -1) {
          type = 2;
        } else if (this.allGun.indexOf(this.$store.state.realTimeMonitor.followCarOrGun) !== -1) {
          type = 1;
        } else {
          return;
        }

        if (this.$store.state.realTimeMonitor.followCarOrGun) {
          //checkNodes的第一个参数原本所需的info对象无法获取，因此直接传递type,在checkNodes中对这种情况单独作了区分
          this.checkNodes(
            type,
            this.$store.state.realTimeMonitor.followCarOrGun, true
          );
        }

      })
    },
    getbindCarOrGun() {

      let p3 = new Promise((resolve, reject) => {
        bindQueryByCars({ cars: this.allCarCode }).then(res1 => {
          if (res1.status == 0) {


            for (let i in res1.data) {
              let imei;
              for (let j in this.carAndGunObj) {
                if (i == this.carAndGunObj[j].code) {
                  imei = j;
                  this.bindCarandgun[imei] = res1.data[i]
                  break;
                }
              }

            }
            resolve();
          } else {
            this.$message.error(res1.msg);
          }
        });
      })
      let p4 = new Promise((resolve, reject) => {
        bindQueryByGuns({ guns: this.allGunCode }).then(res1 => {
          if (res1.status == 0) {
            for (let i in res1.data) {
              let imei;
              for (let j in this.carAndGunObj) {
                if (i == this.carAndGunObj[j].code) {
                  imei = j;
                  this.bindCarandgun[imei] = res1.data[i]

                  break;
                }
              }
            }
            resolve();

          } else {
            this.$message.error(res1.msg);
          }
        });
      })
      return [p3, p4];
    },
    // 改变地图和视频的宽高
