    // 标签页tab 被选中时触发 的方法
    textfunctiong(tab, event) {
      if (tab.name == "first") {
        this.currpage = 1;
      } else if (tab.name == "two") {
        this.listQuery_network.page = 1;
        this.getNetworkList();
      } else if (tab.name == "tree") {
        this.listQuery_route.page = 1;
        this.getRouteList();
      } else if (tab.name == "four") {
        this.currpage1 = 1;
      } else if (tab.name == "five") {
        this.currpage2 = 1;
      }
      $(".el-tabs__content").scrollTop(0);
      this.activeHeight == true
        ? this.maxBootomBox(tab.name)
        : this.shwoBootomBox(tab.name);
