      $("#" + id).height(boxHeight + "px");
    },
    setHeightByClass(className, height) {
      let boxHeight = this.windowHeight - height;
      $("." + className).height(boxHeight + "px");
    },
    //显示报警类型
    getWarningType(row, type) {
      if (row.status == 10001) {
        if (type == 1) {
          return "上线";
        } else {
          return "离线";
        }
      } else if (row.status == 10002) {
        return "GPS信号丢失";
      } else if (row.status == 10003) {
        return "脱离";
      } else if (row.status == 10004) {
        return "车辆周围没有足够的枪支";
      } else if (row.status == 10005) {
        return "车辆关键点打卡超时";
      } else if (row.status == 10006) {
        if (type == 1) {
          return "车辆偏移";
        } else {
          return "车辆路线变动";
        }
      } else if (row.status == 10007) {
        if (type == 1) {
          if (row.info.value !== null && row.info.value !== undefined) {
            return "电量不足 电量:" + row.info.value + "%";
          } else {
            return "电量不足";
          }
        } else {
          if (row.info.value) {
            return "超速 速度:" + row.info.value + "km/h";
          } else {
            return "超速";
          }
        }
      } else if (row.status == 10008) {
        if (row.info.lng && row.info.lat) {
          return (
            "设备破坏 " + "经度:" + row.info.lng + "," + "纬度:" + row.info.lat
          );
        } else {
          return "设备破坏";
        }
      } else if (row.status == 10009) {
        if (type == 22) {
          return "一键报警";
        } else if (type == 300) {
          return "区域超速";
        }
      } else if (row.status == 10011) {
        if (type == 22) {
          return "一键报警";
        } else if (type == 300) {
          return "区域超速";
        }
      } else if (row.status == 10070) {
        return "款箱数量异常"
      } else if (row.status == 10080) {
       return row.info.gunCode+'超时未归还';
      }
    },
    //使用renderContent自定义样式并且实现跟踪和取消跟踪功能
    renderContent(h, { node, data, store }) {
      let that = this;
      // if (data.type !== "device") {
      //   return (
      //     <span>
      //       <i class={data.className}></i>
      //       <span>{node.label}</span>
      //     </span>
      //   );
      // } else 
      if (data.type == "device") {
        return (
          <span>
            <i class={data.className}></i>
            {data.dType == 2 ? (
              <i class={"iconfont  signal iconxinhaoge1 s" + data.id}></i>
            ) : (
              ""
            )}
            {data.dType == 2 ? (
              <i class={"iconfont   iconcloud-disk d" + data.id}></i>
            ) : (
              ""
            )}
            <span
              class={"tree-car p" + data.id}
              on-click={e => {
                if (node.checked == true) {
                  if (e.target.className == "tree-car p" + data.id) {
                    //勾选时先去掉其他已经勾选的关于跟踪状态的节点
                    $("span").removeClass("tree-car-selected");
                    e.target.className =
                      e.target.className + " tree-car-selected";
                    $(".trail").remove();
                    //设置跟踪设备和信息窗口标志
                    this.followCarOrGun = data.id;
                    this.richMarkerImeiIndex = data.id;
                    $(e.target).after(
                      `<div class="trail"><img class="change trail ${this.followCarOrGun == null ? "none" : ""
                      }"  title="跟踪状态(单击左侧取消跟踪)" src="${trailImg}" /></div>`
                    );
                    sessionStorage.historyCode = JSON.stringify(data); // 存储跟踪状态的数据
                    this.setZindex(this.followCarOrGun);
                    let message = null;
                    //勾选实现跟踪功能，打开信息窗口，如果该设备没有获取到信息（经纬度等），则不显示信息窗
                    //实时位置跟踪
                    for (let i = 0; i < that.makerIconArr.length; i++) {
                      if (that.makerIconArr[i].imei == data.id) {
                        message = that.makerIconArr[i];
                        that.addressObj.lastTime = null;
                        that.addressObj.address = '';
                        that.openInfo(
                          message,

                          "changeRichMarkerInfo"
                        );
                        let point = new BMap.Point(message.Blng, message.Blat);
                        that.map.panTo(point);
                        that.myRichMarkerDraged = false;
                        that.myRichMarker.show();

                        break;
                      }
                    }
                    //实时轨迹跟踪
                    for (let item in this.carMkObj) {
                      if (item == data.id) {
                        message = this.carMkObj[item].message;
                        that.addressObj.lastTime = null;
                        that.addressObj.address = '';
                        that.openInfo(
                          message,

                          "changerRealTrail"
                        );
                        let point = this.carMkObj[item].message.point;
                        that.map.panTo(point);
                        that.myRichMarkerDraged = false;
                        that.myRichMarker.show();
                      }

                    }
                    if (!message) {
                      that.myRichMarkerDraged = false;
                      that.myRichMarker.hide();
                    }
                  } else {
                    //取消勾选
                    e.target.className = "tree-car p" + data.id;
                    this.followCarOrGun = null;
                    that.myRichMarkerDraged = false;
                    that.myRichMarker.hide();
                    that.richMarkerImeiIndex = -1;
                    $(".trail").remove();
                  }
                }
              }}
            >
              {node.label}
            </span>
          </span>
        );
      } else if (data.type == "camera") {
        return (
          <span>
            <i class={"iconfont   iconshishishipin c" + data.index + 's' + data.parentId}></i>
            <i class={"iconfont    iconluxiang c" + data.index + 's' + data.parentId}></i>
            <span>{node.label}</span>
          </span>
        );
      } else {
        return (
          <span>
            <i class={data.className}></i>
            <span>{node.label}</span>
          </span>
        );
      }
    },

    //  函数作用：改变枪和车在左侧树状结构显示的图标和在线数量（包括离线图标，上线图标，fake图标）
    //  注：报警的图标不通过这个函数改变
    /*  通过carIsOnlineArr和gunIsOnlineArr分别记录下车和枪的isInline状态，以便于在勾选车或枪是可以根据记录的状态在地图上显示不同的图标
        （注：这个状态的保存只有在勾选的时候起作用，其他时候实时推送的坐标会通过addselected函数传递状态来改变地图上显示的图标） */
    changeInlineStatus(message1, isInline, gunOrCarType, alarm) {
      let message = { ...message1 };
      // isInline  1：在线 2：gps信号丢失(离线) 3：网络异常  5:fake坐标
      let that = this,
        data = [];
      let imei, fake;
      if (!alarm) {
        imei = message.imei;
        fake = message.fake;
      }
      //判断车和枪的类型 gunOrCarType=2为车 gunOrCarType=1为枪
      if (gunOrCarType == 2) {
        data = this.data;
      } else if (gunOrCarType == 1) {
        data = this.gunData;
      } else {
        return;
      }
      if (
        !data ||
        !data.length ||
        !data[0].children ||
        !data[0].children.length
      ) {
        return;
      }

      //改变左侧树状结构的图标，并且通过

      let color = "";
      if (!alarm) {

        if (isInline == 1) {
          //在线
          // color = "blue";
          color = "#3C7EFE";

          if (this.speedObj[message.imei] == 1) {
            color = "#28bb0d";
          }
        } else if (
          isInline == 2
        ) {
          //离线
          // color = "lightgray";
          color = "#333333";
        }  else if (
         
          isInline == 3
        ) {
          //gps信号丢失
          // color = "lightgray";
          color = "#c7bf24 ";
        } else if (isInline == 5) {
          //fake数据
          // color = "fakeColor";
          color = "#ffa82b";

          if (this.speedObj[message.imei] == 1) {
            color = "#28bb0d";
          }
        }else if (isInline == 6) {
         
          color = "7cc0e1 ";
        }
        if (
          sessionStorage.aloneObj &&
          JSON.parse(sessionStorage.aloneObj).hasOwnProperty(message.imei) &&
          JSON.parse(sessionStorage.aloneObj)[message.imei] !== 10007
        ) {
          // color = "red";
          color = "#ff4a4a";
        } else if (
          sessionStorage.aloneObj &&
          JSON.parse(sessionStorage.aloneObj).hasOwnProperty(message.imei) &&
          JSON.parse(sessionStorage.aloneObj)[message.imei] == 10007
        ) {
          color = "blueviolet";
        }

        //判断树状结构列表里面是否有这个设备，因为有可能没有注册的设备也可能会推送上来，
        // 但实际上这个设备不在树状结构里，不用重新计算设备在线数量等
        if (this.carAndGunObj.hasOwnProperty(imei)) {
          if (gunOrCarType == 2) {
            // that.data[0].children[i].children[j].className =
            //   "iconfont iconche1 " + color;

            $(".p" + imei).css("color", color);
            that.changeTreeOnline("car", isInline, imei);
            that.carIsOnlineArr[imei] = isInline;
          } else {
            that.changeTreeOnline("gun", isInline, imei);
            // that.gunData[0].children[i].children[j].className =
            //   "iconfont iconqiangzhi " + color;
            $(".p" + imei).css("color", color);
            that.gunIsOnlineArr[imei] = isInline;
          }
        }

        // break;
      } else if (alarm) {
        for (let i = 0; i < data[0].children.length; i++) {
          for (let j = 0; j < data[0].children[i].children.length; j++) {
            let OnlineArr = {};
            let treeData = [];
            if (gunOrCarType == 2) {
              OnlineArr = this.carIsOnlineArr;
              treeData = this.data;
            } else {
              OnlineArr = this.gunIsOnlineArr;
              treeData = this.gunData;
            }
            let devno = data[0].children[i].children[j].devno;

            if (
              sessionStorage.aloneObj &&
              JSON.parse(sessionStorage.aloneObj).hasOwnProperty(devno) &&
              JSON.parse(sessionStorage.aloneObj)[devno] !== 10007
            ) {
              // color = "red";
              color = "#ff4a4a";
            } else if (
              sessionStorage.aloneObj &&
              JSON.parse(sessionStorage.aloneObj).hasOwnProperty(devno) &&
              JSON.parse(sessionStorage.aloneObj)[devno] == 10007
            ) {
              color = "blueviolet";
            } else {

              let isInline1 = OnlineArr[devno];
              if (isInline1 == 1) {
                //在线
                // color = "blue";
                color = "#3C7EFE";


                if (this.speedObj[devno] == 1) {
                  color = "#28bb0d";
                }
              } else if (
                isInline1 == 2 ||
                isInline1 == 0 ||
                isInline1 == undefined
              ) {
                //离线
                // color = "lightgray";

                color = "#333333";
              } else if (
                isInline1 == 3 
              ) {
                //gps信号丢失
                // color = "lightgray";

                color = "#c7bf24 ";
              } else if (isInline1 == 5) {
                //fake数据
                // color = "fakeColor";
                color = "#ffa82b";
                if (this.speedObj[devno] == 1) {
                  color = "#28bb0d";
                }
              } else if (isInline1 == 6) {
               
                color = "7cc0e1 ";
                
              }
            }
            if (gunOrCarType == 2) {
              $(".p" + devno).css("color", color);
              // treeData[0].children[i].children[j].className =
              //   "iconfont iconche1 " + color;
            } else {
              $(".p" + devno).css("color", color);

              // treeData[0].children[i].children[j].className =
              //   "iconfont iconqiangzhi " + color;
            }
          }
        }
      }
    },

    //函数作用，改变大队在线数量
    //参数：gunOrCarType：'car'（车）'gun'(枪)
    //isInline 1:在线 5：fake 2：gps信号丢失 3：网络异常
    //i, j 从上一个函数传过来，以免再次遍历
    //imei：设备号
    changeTreeOnline(gunOrCarType, isInline, imei) {
      let obj = {},
        data = [],
        that = this;
      if (gunOrCarType == "car") {
        obj = this.carIsOnlineArr;
        data = this.data;
      } else if (gunOrCarType == "gun") {
        obj = this.gunIsOnlineArr;
        data = this.gunData;
      }

      //改变在线数量
      if (obj.hasOwnProperty(imei)) {
        if (obj[imei] !== isInline) {
          if (
            (obj[imei] == 1 || obj[imei] == 5||obj[imei] == 6|| obj[imei] == 3) &&
            (isInline == 2  || isInline == 0)
          ) {
            //数量减少
            that.getTreeOnlineNum(gunOrCarType, "reduce", imei);
          }
          if (
            (obj[imei] == 2  || obj[imei] == 0) &&
            (isInline == 1 || isInline == 5||isInline == 6|| isInline == 3)
          ) {
            //数量增加
            that.getTreeOnlineNum(gunOrCarType, "add", imei);
          }
        }
      } else {
        //第一次记录
        if (isInline == 1 || isInline == 5|| isInline == 6|| isInline == 3) {
          that.getTreeOnlineNum(gunOrCarType, "add", imei);
        }
      }
    },
    //函数功能：改变车和枪的在线数量显示（左侧树状结构）
    //gunOrCarType car:车 gun:枪
    //i,j 为了避免再次for循环查找，从上一个函数传递过来
    //addOrReduce:'add'：增加 'reduce':减少
    getTreeOnlineNum(gunOrCarType, addOrReduce, imei) {
      let data = [];
      if (gunOrCarType == "car") {
        data = this.data;
        name = "车";
      } else if (gunOrCarType == "gun") {
        data = this.gunData;
        name = "枪";
      }
      for (let i = 0; i < data[0].children.length; i++) {
        for (let j = 0; j < data[0].children[i].children.length; j++) {
          if (data[0].children[i].children[j].id == imei) {
            //数量加
            if (addOrReduce == "add") {
              data[0].children[i].isOnlineNum += 1;
              data[0].children[i].name =
                data[0].children[i].teamName1 +
                "(" +
                data[0].children[i].isOnlineNum +
                "/" +
                data[0].children[i].children.length +
                ")";
              data[0].inlineNum += 1;
              data[0].name =
                this.company +
                "(" +
                name +
                data[0].inlineNum +
                "/" +
                data[0].totalNum +
                ")";
            } else if (addOrReduce == "reduce") {
              //数量减
              if (data[0].children[i].isOnlineNum > 0) {
                data[0].children[i].isOnlineNum -= 1;
                data[0].children[i].name =
                  data[0].children[i].teamName1 +
                  "(" +
                  data[0].children[i].isOnlineNum +
                  "/" +
                  data[0].children[i].children.length +
                  ")";
                if (data[0].inlineNum > 0) {
                  data[0].inlineNum -= 1;
                  data[0].name =
                    this.company +
                    "(" +
                    name +
                    data[0].inlineNum +
                    "/" +
                    data[0].totalNum +
                    ")";
                }
              }
            }
            break;
          }
        }
      }
    },

    handleCurrentChange(cpage) {
      this.currpage = cpage;
      $(".el-tabs__content").scrollTop(0);
    },
    handleSizeChange(psize) {
      this.pagesize = psize;
    },
    handleCurrentChange1(cpage) {
      this.currpage1 = cpage;
      $(".el-tabs__content").scrollTop(0);
    },
    handleSizeChange1(psize) {
      this.pagesize1 = psize;
    },
    handleCurrentChange2(cpage) {
      this.currpage2 = cpage;
      $(".el-tabs__content").scrollTop(0);
    },
    handleSizeChange2(psize) {
      this.pagesize2 = psize;
    },
    handleCurrentChange3(cpage) {
      this.currpage3 = cpage;
      $(".el-tabs__content").scrollTop(0);
    },
    handleCurrentChange4() {
      $(".el-tabs__content").scrollTop(0);
    },
    handleSizeChange3(psize) {
      this.pagesize3 = psize;
    },
    handleSelectionChange(val) { },
    //显示设备类型
    formatCarOrGunType(data) {
      if (
        data.status == 10001 ||
        data.status == 10002 ||
        data.status == 10003 ||
        data.status == 10007 ||
        data.status == 10008 ||
        data.status == 10070 ||
        data.status == 10080
      ) {
        if (data.info.type == 1) {
          return "枪";
        } else {
          return "车";
        }
      } else {
        return "车";
      }
    },
    //获取网点列表并且对数据进一步处理，增加查看属性check 默认check = 1为未被查看状态，按钮会显示成查看
    getNetworkList() {
      // 生成列表
      let _t = this;
      _t.listLoading_network = true;
      lstPt({
        type: _t.networkVal,
        page: _t.listQuery_network.page,
        limit: _t.listQuery_network.limit
      })
        .then(res => {
          if (res.status == 0) {
            _t.listLoading_network = false;
            _t.list_network = res.data.records;
            _t.list_network.forEach((item, index) => {
              _t.list_network[index].index = index;
              //如果改网点已经被查看，check=0,按钮会显示成隐藏
              //否则，check=1,按钮会显示成查看
              //_t.networkMarkerObj记录所有被查看过的网点 _t.networkPolygonObj记录所有被查看过得网点圆
              if (_t.networkMarkerObj.hasOwnProperty(item.id)) {
                _t.list_network[index].check = 0;
              } else {
                _t.list_network[index].check = 1;
              }
              if (_t.networkPolygonObj.hasOwnProperty(item.id)) {
                _t.list_network[index].check = 0;
              } else {
                _t.list_network[index].check = 1;
              }

              _t.list_network[index].createTime = formatDate(
                item.createTime,
                1
              );
              _t.list_network[index].networkType =
                _t.networkList[item.contentType].option;
            });
            _t.total_network = res.data.total;
            _t.indexMethod =
              (_t.listQuery_network.page - 1) * _t.listQuery_network.limit + 1;
          } else {
            _t.$message.error(res.msg);
          }
        })
        .finally(() => {
          _t.listLoading_network = false;
        });
    },
    // 查询网点
    handleFilter(e) {
      this.networkVal = e;
      this.listQuery_network.page = 1;
      this.deleteAllNetwork();

      this.getNetworkList();
    },

    //点击查看网点发生的事件
    //添加网点 网点圆形覆盖物 或者添加多边形覆盖物

    checkNetwork(row) {
      row.check = 0;
      this.list_network.splice(row.index, 1, row);
      if (row.contentType !== 7) {
        let pointArr = this.getLngAndLat(row.geom);
        this.addNetWork(pointArr[0], pointArr[1], row);
        this.addCircle(pointArr[0], pointArr[1], row);
      } else {
        //添加多边形覆盖物
        let geoms = row.geom.slice(9, -2);
        this.drawPolygon(geoms, row);
      }
    },
    //从地图上移除网点覆盖物
    deleteNetwork(row) {
      row.check = 1;
      this.list_network.splice(row.index, 1, row);
      this.map.removeOverlay(this.networkMarkerObj[row.id]);
      this.map.removeOverlay(this.networkCircleObj[row.id]);

      delete this.networkMarkerObj[row.id];
      delete this.networkCircleObj[row.id];
      if (row.contentType == 7) {
        this.map.removeOverlay(this.networkPolygonObj[row.id]);
        delete this.networkPolygonObj[row.id];
      }
    },
    //从地图上移除网点覆盖物
    deleteSingleRoute(row) {
      row.check = 1;
      this.list_route.splice(row.index, 1, row);
      for (let i = 0; i < this.allRoute.length; i++) {
        if (row.geometryId == this.allRoute[i].geometryId) {
          this.map.removeOverlay(this.allRoute[i].polyline);
          this.map.removeOverlay(this.allRoute[i].marker1);
          this.map.removeOverlay(this.allRoute[i].marker2);
          this.map.removeOverlay(this.allRoute[i].pointCollection);
          this.allRoute.splice(i, 1);
          delete this.networkRouteObj[row.geometryId];
        }
      }
    },
    //移除路线
    deleteRoute() {
      for (let i = 0; i < this.list_route.length; i++) {
        this.list_route[i].check = 1;
      }
      let aa = this.list_route[this.list_route.length - 1];
      this.list_route.pop();
      this.list_route.push(aa);
      for (let i = 0; i < this.allRoute.length; i++) {
        this.map.removeOverlay(this.allRoute[i].polyline);
        this.map.removeOverlay(this.allRoute[i].marker1);
        this.map.removeOverlay(this.allRoute[i].marker2);
        this.map.removeOverlay(this.allRoute[i].pointCollection);
      }
      this.allRoute = [];
      this.networkRouteObj = {};
    },
    //移除所有网点  这样循环的原因是可能地图上还有设备的marker不能直接一起移除
    deleteAllNetwork() {
      if (this.list_network.length == 0) {
        return;
      }
      for (let i in this.networkMarkerObj) {
        this.map.removeOverlay(this.networkMarkerObj[i]);
      }
      for (let i in this.networkCircleObj) {
        this.map.removeOverlay(this.networkCircleObj[i]);
      }
      for (let i in this.networkPolygonObj) {
        this.map.removeOverlay(this.networkPolygonObj[i]);
      }
      for (let i = 0; i < this.list_network.length; i++) {
        this.list_network[i].check = 1;
      }
      let aa = this.list_network[this.list_network.length - 1];
      this.list_network.pop();
      this.list_network.push(aa);
      this.networkMarkerObj = {};
      this.networkCircleObj = {};
      this.networkPolygonObj = {};
    },
    //显示全部网点
    showAllNetwork() {
      //先把地图上已经存在的网点全部删除，再重新添加所有网点
      this.deleteAllNetwork();
      for (let i = 0; i < this.list_network.length; i++) {
        this.checkNetwork(this.list_network[i]);
      }
    },

    //格式化所获取的经纬度
    getLngAndLat(data) {
      let arr = data.split("(")[1];
      arr = arr.split(")")[0];
      arr = arr.split(" ");
      return arr;
    },
    //添加网点图标及网点信息窗口
    addNetWork(lng, lat, row) {
      let p1 = new BMap.Point(lng, lat);
      let that = this;
      let networkMarker = null;
      networkMarker = new BMap.Marker(p1); // 创建标注
      this.networkMarkerObj[row.id] = networkMarker;
      this.map.addOverlay(networkMarker); // 将标注添加到地图中
      let opts = {
        // title: "网点信息" // 信息窗口标题
        offset: new BMap.Size(-116, 50)
      };
      let html = `<div class="contentBox"><ul class="myNetworkInfo">
            <li><i class="eid">网点信息</i></li>
            <li class="${this.power == 1 ? "" : "hide"}">ID：<i class="eid">${row.id
        }</i></li>
            <li>网点类型：<i>${row.networkType}</i></li>
             <li>网点名称：<i>${row.name}</i></li>
          </ul></div>`;
      let infoWindow = new BMap.InfoWindow(html, opts); //弹出窗口
      networkMarker.addEventListener("click", function () {
        this.openInfoWindow(infoWindow);
        networkMarker = null;
      });
    },
    //添加网点圆形范围
    addCircle(lng, lat, row) {
      let mPoint = new BMap.Point(lng, lat);
      let networkCircle = new BMap.Circle(mPoint, row.radius, {
        fillColor: "blue",
        strokeWeight: 1,
        fillOpacity: 0.3,
        strokeOpacity: 0.3
      });
      this.networkCircleObj[row.id] = networkCircle;
      this.map.addOverlay(networkCircle);
    },
    // 画多边形
    drawPolygon(list, row) {
      let t = this,
        map = t.map,
        paths = list.split(","),
        result = [];
      paths.pop();
      paths.forEach(x => {
        let res = x.split(" ");
        let bmapPoint = new BMap.Point(res[0], res[1]);
        result.push(bmapPoint);
      });
      // 添加多边形
      let myPolygon = new BMap.Polygon(result, {
        strokeColor: "red", //边线颜色。
        fillColor: "#e4ff02", //填充颜色。当参数为空时，圆形将没有填充效果。
        strokeWeight: 3, //边线的宽度，以像素为单位。
        strokeOpacity: 0.8, //边线透明度，取值范围0 - 1。
        fillOpacity: 0.5, //填充的透明度，取值范围0 - 1。
        strokeStyle: "solid" //边线的样式，solid或dashed。
      });
      myPolygon.setFillColor("orange");
      t.networkPolygonObj[row.id] = myPolygon;

      map.addOverlay(myPolygon);
      let opts = {
        // title: "网点信息" // 信息窗口标题
      };
      let html = `<div class="contentBox"><ul class="myNetworkInfo">
            <li><i class="eid">网点信息</i></li>
            <li class="${this.power == 1 ? "" : "hide"}">ID：<i class="eid">${row.id
        }</i></li>
            <li>网点类型：<i>${row.networkType}</i></li>
             <li>网点名称：<i>${row.name}</i></li>
            <li>地址：<i>${row.address}</i></li>
          </ul></div>`;
      let infoWindow = new BMap.InfoWindow(html, opts); //弹出窗口
      myPolygon.addEventListener("click", function () {
        this.openInfoWindow(infoWindow);
        myPolygon = null;
      });
    },
    //获取路线列表并处理数据
    // check=0查看状态 check=1为未查看状态
    //_t.networkRouteObj记录该条路线是否已经显示在地图上
    getRouteList() {
      let _t = this;
      _t.listLoading = true;
      roadlist({
        page: _t.listQuery_route.page,
        limit: _t.listQuery_route.limit
      })
        .then(res => {
          if (res.status == 0) {
            _t.listLoading = false;
            _t.list_route = res.data.records;
            _t.list_route.forEach((item, index) => {
              _t.list_route[index].index = index;
              if (_t.networkRouteObj.hasOwnProperty(item.geometryId)) {
                _t.list_route[index].check = 0;
              } else {
                _t.list_route[index].check = 1;
              }
              _t.list_route[index].createTime = formatDate(item.createTime);
            });
            _t.total_route = res.data.total;
            _t.indexMethod_route =
              (_t.listQuery_route.page - 1) * _t.listQuery_route.limit + 1;
          } else {
            _t.$message.error(res.msg);
          }
        })
        .finally(() => {
          _t.listLoading = false;
        });
    },
    //查看路线
    checkRoute(row) {
      let _t = this,
        map = _t.map,
        geomList = [],
        lineOptions = {
          // 折线样式
          strokeOpacity: 1,
          strokeColor: "#3f9c0f",
          strokeWeight: 4
        },
        geomData = [];
      //根据路线id获取路线并显示在地图上
      if (row.geometryId == null) {
        this.$message.warning('该路线未画实际路线！');
        return;

      }
      viewlist({ geometryId: row.geometryId }).then(res => {
        if (res.status == 0) {
          row.check = 0;
          _t.list_route.splice(row.index, 1, row);
          _t.networkRouteObj[row.geometryId] = 1;
          geomList = res.data.geom.split(",");
          geomList.forEach(x => {
            geomData.push(new BMap.Point(x.split(" ")[0], x.split(" ")[1]));
          });
          map.setViewport(geomData, { margins: [50, 50, 50, 50] });
          let polyline = new BMap.Polyline(geomData, lineOptions);
          map.addOverlay(polyline);
          // 绘制海量点
          let marker1 = new BMap.Marker(geomData[0], { icon: _t.markerStart }); // 开始图标
          map.addOverlay(marker1);
          let marker2 = new BMap.Marker(geomData[geomData.length - 1], {
            icon: _t.markerEnd
          }); // 结束图标
          map.addOverlay(marker2);
          let pointCollection = new BMap.PointCollection(geomData, {
            color: "#d340c3",
            size: BMAP_POINT_SIZE_SMALL
          });
          map.addOverlay(pointCollection);
          this.allRoute.push({
            polyline: polyline,
            marker1: marker1,
            marker2: marker2,
            pointCollection: pointCollection,
            geometryId: row.geometryId
          });
        } else {
          _t.$message.error(res.msg);
        }
      });
    },
    //函数作用：点击勾选的时候删除页面中刚被取消选择的轨迹，增加页面中刚被选中的轨迹
    //this.realtimeTrailObj 是所有的轨迹
    formateRealtimeTrailObj() {
      //所有的枪和车数组
      let selectCarOrGunArray = this.selectCarArray.concat(this.selectGunArray);
      let realtimeTrailObj = { ...this.realtimeTrailObj };
      //从所有的轨迹中删除取消勾选的轨迹
      for (let item in realtimeTrailObj) {
        if (
          !selectCarOrGunArray.find(function (elem) {
            return elem.id == item;
          })
        ) {
          for (let i = 0; i < realtimeTrailObj[item].polyline.length; i++) {
            this.map.removeOverlay(realtimeTrailObj[item].polyline[i]);
          }

          this.map.removeOverlay(this.carMkStartObj[item]);
          delete this.carMkStartObj[item];
          this.map.removeOverlay(this.carMkObj[item]);
          if (this.carMkObj[item]) {
            // this.map.removeOverlay(this.carMkObj[item].message.IconLabel);

          }

          delete this.carMkObj[item];
          delete this.realtimeTrailObj[item];
        }
      }
      //添加新被勾选的轨迹
      let realtimeTrailObjNew = { ...this.realtimeTrailObj };
      for (let i = 0; i < selectCarOrGunArray.length; i++) {
        if (!realtimeTrailObjNew.hasOwnProperty(selectCarOrGunArray[i].id)) {
          let imei = selectCarOrGunArray[i].id;
          this.realtimeTrailObj[selectCarOrGunArray[i].id] = {};
          this.realtimeTrailObj[selectCarOrGunArray[i].id].polyline = [];
          this.realtimeTrailObj[selectCarOrGunArray[i].id].lastPoint = null;
          this.realtimeTrailObj[selectCarOrGunArray[i].id].points = [[]];
        }
      }
    },
    //函数功能：从实时位置到实时轨迹显示时初始化轨迹对象并且添加起点
    posToTrailAddStartMarker(makerIconArr) {
      //所有的枪和车数组
      let selectCarOrGunArray = this.selectCarArray.concat(this.selectGunArray);
      //初始化被选中设备轨迹显示对象
      for (let i = 0; i < selectCarOrGunArray.length; i++) {
        let imei = selectCarOrGunArray[i].id;
        this.realtimeTrailObj[selectCarOrGunArray[i].id] = {};
        this.realtimeTrailObj[selectCarOrGunArray[i].id].polyline = [];
        this.realtimeTrailObj[selectCarOrGunArray[i].id].lastPoint = null;
        this.realtimeTrailObj[selectCarOrGunArray[i].id].points = [[]];
      }
      //添加起点
      for (let j = 0; j < makerIconArr.length; j++) {
        let obj = {};
        if (makerIconArr[j].type == 2) {
          obj = this.carIsOnlineArr;
        } else if (makerIconArr[j].type == 1) {
          obj = this.gunIsOnlineArr;
        }

        if (
          obj.hasOwnProperty(makerIconArr[j].imei) &&
          (obj[makerIconArr[j].imei] == 1 || obj[makerIconArr[j].imei] == 5)
        ) {
          //添加图标的时候根据fake这个改变图标
          if (obj[makerIconArr[j].imei] == 1) {
            makerIconArr[j].fake = 0;
          } else if (obj[makerIconArr[j].imei] == 5) {
            makerIconArr[j].fake = 1;
          }

          this.addRealtimeTrail(
            makerIconArr[j],
            makerIconArr[j].type,
            obj[makerIconArr[j].imei]
          ); //添加起点
        }
      }
    },
    //添加轨迹或者改变轨迹中设备的图标和信息
    //this.realtimeTrailObj 是所有现存轨迹的信息（只要是被勾选了，不管有没有轨迹在地图上，this.realtimeTrailObj都会初始化这个设备的轨迹信息）
    // this.realtimeTrailObj 的属性： lastPoint：上一个点百度坐标 time：时间 isDotLast：这个设备最近画的这一段轨迹是实现还是虚线 points：这个设备多段路线的所有点信息
    //一个设备的轨迹由于有实线（30s内间隔画线）有虚线（超过30s画线），所以是分很多段的，如果前一段是虚线，这一段是实线则会新增一段，如果前一段是实线后一段也是实线，则是替换前一段成为新的更长的轨迹线（setpath）
    addRealtimeTrail(newdata1, gunOrCarType, status) {
      let newdata = { ...newdata1 };
      //转换成的百度地图坐标
      let resultPoint; //当前最新一个轨迹点的百度经纬度
      let message = null; //改设备在更新轨迹之前的最新轨迹信息
      //给当前将要添加的轨迹点添加百度经纬度属性
      if (newdata.lng && newdata.lat) {
        let point = new BMap.Point(newdata.lng, newdata.lat);
        resultPoint = Gps2Bd(point);
        newdata.Blng = resultPoint.lng;
        newdata.Blat = resultPoint.lat;
      }
      //如果地图上已经存在该设备的轨迹，获取该设备的信息
      if (this.realtimeTrailObj.hasOwnProperty(newdata.imei)) {
        message = { ...this.realtimeTrailObj[newdata.imei] };
      }
      //只有是1在线状态和5fake状态才会确定起点或者是画轨迹,其他状态都是改变终点设备的图标，不会画轨迹

      if (
        (status == 1 || status == 5) &&
        newdata.lng &&
        newdata.lat &&
        this.realtimeTrailObj.hasOwnProperty(newdata.imei)
      ) {
        //默认一开始勾选就执行以下代码（message.lastPoint==null代表这个设备已经被勾选，但是还没画轨迹，因此要添加起点）

        if (message.lastPoint == null) {
          //第一次获取点，并添加起点
          this.addStartMarker(resultPoint, "起点", newdata);
          message.lastPoint = resultPoint;
          message.time = newdata.time;
          message.isDot = false;
          message.points[0].push(
            new BMap.Point(resultPoint.lng, resultPoint.lat)
          ); //放入起点
          message.polyline = [];
          this.realtimeTrailObj[newdata.imei] = { ...message };
        }
        //  else {
        //划线

        //间隔超过3分钟，重新画线（虚线）
        let len1 = message.points.length; //单个设备总画线数
        let isDot = false; //判断这次是画虚线还是实线
        let lastPointBD = new BMap.Point(
          message.lastPoint.lng,
          message.lastPoint.lat
        ); //上一个点
        let isAdd = true; //判断是添加线还是替换线
        let Dval = newdata.time - message.time; //时间间隔
        let isDotLast = message.isDot; //判断上一次是否是虚线
        //画线间隔超过30s则是虚线
        //第一次画线默认上一次画的是虚线
        if (message.polyline.length == 0 && !(Dval > 180000)) {
          isDotLast = true;
        }
        //如果间隔超过30s并且上一次画的是实线，则这一次画的是虚线，需要新增轨迹段
        if (Dval > 180000 && isDotLast == false) {
          message.points.push([
            lastPointBD,
            new BMap.Point(resultPoint.lng, resultPoint.lat)
          ]); //新增一条线
          message.isDot = true;
          isDot = true;
          isAdd = true;
          //如果间隔超过30s并且上一次画的是虚线，则这一次画的是虚线，需要替换轨迹段
        } else if (Dval > 180000 && isDotLast == true) {
          message.points[len1 - 1].push(
            new BMap.Point(resultPoint.lng, resultPoint.lat)
          );
          isDot = true;
          isAdd = false;
          //如果间隔不超过30s并且上一次画的是虚线，则这一次画的是实线，需要增加轨迹段
        } else if (!(Dval > 180000) && isDotLast == true) {
          message.points.push([
            lastPointBD,
            new BMap.Point(resultPoint.lng, resultPoint.lat)
          ]); //新增一条线
          message.isDot = false;
          isDot = false;
          isAdd = true;
          //如果间隔不超过30s并且上一次画的是是线，则这一次画的是实线，需要替换轨迹段
        } else if (!(Dval > 180000) && isDotLast == false) {
          message.isDot = false;
          isDot = false;
          isAdd = false;
          message.points[len1 - 1].push(
            new BMap.Point(resultPoint.lng, resultPoint.lat)
          );
        }
        //画线（包括新增轨迹段和替换轨迹段），并且添加重点坐标
        this.drowLine(
          message,
          resultPoint,
          gunOrCarType,
          newdata,
          isDot,
          isAdd
        );
        message.lastPoint = resultPoint;
        message.time = newdata.time;
        this.realtimeTrailObj[newdata.imei] = { ...message };
        // }
        //只有不为在线和fake状态才是替换终端图标，这个时候跟上一个坐标点没有变，只是改变重点图标的颜色和信息而已
      } else if (
        (status !== 1 || (status == 1 && !newdata.lng && !newdata.lat)) &&
        (status !== 5 || (status == 1 && !newdata.lng && !newdata.lat)) &&
        this.realtimeTrailObj.hasOwnProperty(newdata.imei) &&
        message.lastPoint !== null
      ) {
        //避免只是改变图标颜色时离线状态等没有坐标上传等情况信息框点击不能出现等问题
        if (
          !newdata.lng &&
          !newdata.lat &&
          this.carMkObj.hasOwnProperty(newdata.imei)
        ) {
          newdata.lng = this.carMkObj[newdata.imei].message.lng;
          newdata.lat = this.carMkObj[newdata.imei].message.lat;
          newdata.Blng = message.lastPoint.lng;
          newdata.Blat = message.lastPoint.lat;
        }

        //
        let obj = message;
        let PointArrNext = message.lastPoint;
        this.addMarkerEnd(
          new BMap.Point(PointArrNext.lng, PointArrNext.lat),
          gunOrCarType,
          newdata,
          status
        );
      }
    },
    //从所有的轨迹中删除没有被选中的项
    //notEmptyCarOrGun 是在执行从实时轨迹切换到实时位置时的操作，其他清空操作这个参数都可不传
    delRealTimeTrail(notEmptyCarOrGun) {
      let realtimeTrailObj = this.realtimeTrailObj;
      for (let item in this.realtimeTrailObj) {
        if (realtimeTrailObj[item].polyline) {
          for (let i = 0; i < realtimeTrailObj[item].polyline.length; i++) {
            this.map.removeOverlay(realtimeTrailObj[item].polyline[i]);
          }
        }
        this.map.removeOverlay(this.carMkStartObj[item]);

        delete this.carMkStartObj[item];
        if (notEmptyCarOrGun !== "notEmptyCarOrGun") {
          this.map.removeOverlay(this.carMkObj[item]);
          // this.map.removeOverlay(this.carMkObj[item].message.IconLabel);

        }
        delete this.carMkObj[item];

        delete this.realtimeTrailObj[item];
      }
      this.realtimeTrailObj = {};
      this.carMkStartObj = {};
      this.carMkObj = {};
    },
    delMakerIcon() {
      for (let i = 0; i < this.makerIconArr.length; i++) {
        this.map.removeOverlay(this.makerIconArr[i].Icon);
        // this.map.removeOverlay(this.makerIconArr[i].IconLabel);

      }
      this.makerIconArr = [];
    },
    //添加起始图标
    addStartMarker(point, name, newdata) {
      if (name == "起点") {
        let myIcon = new BMap.Icon(mapIcon, new BMap.Size(26, 41), {
          anchor: new BMap.Size(13, 40),
          imageOffset: new BMap.Size(-199, -139)
        });
        let marker = new BMap.Marker(point, { icon: myIcon });
        this.map.addOverlay(marker); // 将标注添加到地图中
        this.carMkStartObj[newdata.imei] = marker;
      }
    },
    // 划线并且增加终点图标
    // 参数解释：trailObj：需要画线或替换线的轨迹信息  PointArrNext：最新一个轨迹点的百度经纬度 gunOrCarType：车或枪类型
    //参数解释：newdata：最新的轨迹点信息 isDot这个线是虚线还是实线 isAdd：是添加线还是替换线（setPath）
    drowLine(trailObj, PointArrNext, gunOrCarType, newdata, isDot, isAdd) {
      let len1 = trailObj.points.length;
      let points = trailObj.points[len1 - 1];
      let polyline = null;
      if (isAdd) {
        if (isDot) {
          polyline = new BMap.Polyline(points, {
            strokeStyle: "dashed",
            strokeOpacity: 1,
            strokeColor: "#a7a7a7",
            strokeWeight: 4
          }); //创建折线
        } else {
          polyline = new BMap.Polyline(points, {
            strokeColor: "#3f9c0f",
            strokeWeight: 3,
            strokeOpacity: 1
          }); //创建折线
        }
        //画一条新的线
        let len = this.realtimeTrailObj[newdata.imei].polyline.length;
        this.realtimeTrailObj[newdata.imei].polyline.push(polyline);
        this.map.addOverlay(polyline);
      } else {
        // 替换线
        let len = this.realtimeTrailObj[newdata.imei].polyline.length;
        this.realtimeTrailObj[newdata.imei].polyline[len - 1].setPath(points);
      }
      let fake = 1;
      //代表这个点是fake点
      if (newdata.fake == 1) {
        fake = 5;
      }
      // 添加设备或修改设备图标和信息
      this.addMarkerEnd(
        new BMap.Point(PointArrNext.lng, PointArrNext.lat),
        gunOrCarType,
        newdata,
        fake
      );
    },
    //添加设备图标和或修改图标和信息
    //参数解释：point当前要修改或添加的点  gunOrCarType车或者枪 newdata当前轨迹点信息 status要添加或修改点的状态（离线上线或fake等）
    addMarkerEnd(point, gunOrCarType, newdata, status, alarm) {
      let that = this;
      let end = null;
      //各种图标
      let onlineCar1 = onlineCar;
      let onlineGun1 = onlineGun;
      let offlineCar1 = offlineCar;
      let offlineGun1 = offlineGun;
      let fakecar1 = fakecar;
      let fakegun1 = fakegun;
      let gpsCar1 = gpsCar;
      let gpsGun1 = gpsGun;
      let aloneGun1 = aloneGun;
      let aloneCar1 = aloneCar;
      let noPositionCar1 = noPositionCar;
      let noPositionGun1 = noPositionGun;
      let aloneObj1 = JSON.parse(sessionStorage.aloneObj); //脱离等需要把设备颜色变为闪烁红的设备
      //根据status确定要添加的图标
      if (status == undefined) {
        if (gunOrCarType == 1) {
          end = onlineGun1;
        } else {
          end = onlineCar1;
        }
      } else {
        if (status == 0 || status == 2) {
          if (gunOrCarType == 2) {
            end = offlineCar1;
          } else {
            end = offlineGun1;
          }
        } else if (status == 3) {
          if (gunOrCarType == 2) {
            end = gpsCar2;
          } else {
            end = gpsGun2;
          }
        } else if (status == 1) {
          if (gunOrCarType == 2) {
            end = onlineCar1;
          } else {
            end = onlineGun1;
          }
        } else if (status == 4) {
          if (gunOrCarType == 2) {
            end = gpsCar1;
          } else {
            end = gpsGun1;
          }
        } else if (status == 5 && this.power == 1) {
          if (gunOrCarType == 2) {
            end = fakecar1;
          } else {
            end = fakegun1;
          }
        } else if (status == 5 && this.power !== 1) {
          if (gunOrCarType == 2) {
            end = onlineCar1;
          } else {
            end = onlineGun1;
          }
        }else if (status == 6) {
          if (gunOrCarType == 2) {
            end = noPositionCar1;
          } else {
            end = noPositionGun1;
          }
        }
      }



      if ((end == onlineCar1 || end == fakecar1) && that.speedObj[newdata.imei] == 1) {
        end = speedCar;
      }
      if ((end == onlineGun1 || end == fakegun1) && that.speedObj[newdata.imei] == 1) {
        end = speedGun;
      }

      //如果是在线设备且速度为0则换为停止图标
      //在status确定图标的基础上看该轨迹点是否处于正在是红色的报警状态来重新改变图标
      if (
        aloneObj1.hasOwnProperty(newdata.imei) &&
        aloneObj1[newdata.imei] !== 10007
      ) {
        if (newdata.type == 1) {
          end = aloneGun1;
        } else if (newdata.type == 2) {
          end = aloneCar1;
        }
      } else if (
        aloneObj1.hasOwnProperty(newdata.imei) &&
        aloneObj1[newdata.imei] == 10007
      ) {
        if (newdata.type == 1) {
          end = gpsGun1;
        } else if (newdata.type == 2) {
          end = gpsCar1;
        }
      }
      //终点图标（设备）
      let terminalPoint = new BMap.Icon(end, this.bmapSize, {
        imageSize: this.bmapSize
      });
      //报警改变状态
      if (this.carMkObj.hasOwnProperty(newdata.imei) && alarm == 'alarm') {
        this.carMkObj[newdata.imei].setIcon(terminalPoint);
        if (newdata.type == 2) {
          this.carMkObj[newdata.imei].setRotation(this.carMkObj[newdata.imei].getRotation());
        }
        return;
      }
      //如果已经添加过设备图标 this.carMkObj是实时轨迹所有添加在地图上的设备marker
      if (this.carMkObj.hasOwnProperty(newdata.imei)) {
        // newdata.IconLabel=this.carMkObj[newdata.imei].message.IconLabel;
        this.carMkObj[newdata.imei].message = newdata;
        //改变设备位置
        if (status == 1 || status == 5) {
          this.carMkObj[newdata.imei].setPosition(point);
          // this.carMkObj[newdata.imei].message.IconLabel.setPosition(point);

        }
        //改变图标
        this.carMkObj[newdata.imei].setIcon(terminalPoint);
        //改变设备方向（车辆才会改变，枪支不用）
        if (
          (status == 1 || status == 5 || status == undefined) &&
          gunOrCarType == 2
        ) {
          if (newdata.direction !== undefined) {
            this.carMkObj[newdata.imei].setRotation(newdata.direction + 90); // 旋转的角度
          }
        } else {
          this.carMkObj[newdata.imei].setRotation(
            this.carMkObj[newdata.imei].getRotation()
          );
        }
      } else {
        //第一次添加该设备marker
        this.carMkObj[newdata.imei] = new BMap.Marker(point, {
          icon: terminalPoint
        }); // 创建标注
        this.carMkObj[newdata.imei].message = newdata;
        if (gunOrCarType == 2 && (status == undefined || status == 1)) {
          // 旋转的角度
          let direction = 0;
          if (newdata.direction !== undefined) {
            direction = newdata.direction + 90;
          } else {
            direction = -90;
          }
          this.carMkObj[newdata.imei].setRotation(direction); // 旋转的角度
        } else {
          this.carMkObj[newdata.imei].setRotation(
            that.carMkObj[newdata.imei].getRotation()
          );
        }
        this.addClickHandler(
          this.carMkObj[newdata.imei].message,
          this.carMkObj[newdata.imei],
          "addMarkerEnd"
        );
        let licenseNo = "";
        if (this.carAndGunObj.hasOwnProperty(newdata.imei)) {

          licenseNo = this.carAndGunObj[newdata.imei].code;
        }
        this.carMkObj[newdata.imei].setLabel(this.setLabelStyle(licenseNo));

        // this.carMkObj[newdata.imei].message.IconLabel=new BMap.Label(licenseNo,{position : point, offset: new BMap.Size(0,0)});
        this.map.addOverlay(this.carMkObj[newdata.imei]); // 将标注添加到地图中

        // this.map.addOverlay(this.carMkObj[newdata.imei].message.IconLabel); // 将标注添加到地图中



      }

      if (end == aloneGun1 || end == aloneCar1) {
        this.carMkObj[newdata.imei].setZIndex(2);
      } else {
        this.carMkObj[newdata.imei].setZIndex(1);
      }
      //跟踪车辆或枪支
      if (newdata.imei == this.followCarOrGun) {
        this.bound = this.map.getBounds();
        if (!this.bound.containsPoint(point)) {
          this.map.panTo(point);
        }
      }
      // 富标注实时定位
      if (
        newdata.imei === this.richMarkerImeiIndex &&
        (status == undefined || status == 1 || status == 5)
      ) {
        this.openInfo(newdata, "changeRichMarkerInfo");
      } else if (newdata.imei === this.richMarkerImeiIndex) {
        this.openInfo(newdata, "changerRealTrail");
      }
    },
    //添加或改变实时位置
    addSelected(data, type) {
      let message = { ...data.message };
      if (this.selectObj.hasOwnProperty(message.imei)) {

        this.addCarToMap(message, type);
      }
    },
    //改变离线超时设备信息
    getOffline(val) {
      this.currpage1 = 1;
      if (val == 2) {
        this.offlineTimeoutArr = this.carOfflineTimeoutArr;
      } else {
        this.offlineTimeoutArr = this.gunOfflineTimeoutArr;
      }
    },
    //请求得到设备的最后在线时间，过滤得到离线超时的设备
    getLastGps(type) {
      let arr = [];
      if (type == 2) {
        arr = [...this.allCar];
      } else {
        arr = [...this.allGun];
      }
      lastgps(arr).then(res => {
        if (res && res.status == 0) {
          if (res.data !== [] && res.data) {
            this.getOfflineTimeout(res.data, type);
          }
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    //得到离线超速的设备
    getOfflineTimeout(infos, type) {
      let offlineTimeoutArr = [];
      let timeNow = Date.parse(new Date());
      for (let i = 0; i < infos.length; i++) {
        let gpsMes = infos[i];
        if (gpsMes == null) {
          if (type == 1) {
            offlineTimeoutArr.push({ imei: this.allGun[i] });
          } else {
            offlineTimeoutArr.push({ imei: this.allCar[i] });
          }
          continue;
        }

        let interval = parseInt((timeNow - parseInt(gpsMes.time)) / 1000);
        if (interval > 2 * 24 * 3600) {
          let day = 0,
            hour = 0,
            minute = 0,
            second = 0;
          day = parseInt(interval / (24 * 3600));
          hour = parseInt((interval % (24 * 3600)) / 3600);
          minute = parseInt(((interval % (24 * 3600)) % 3600) / 60);
          second = parseInt(((interval % (24 * 3600)) % 3600) % 60);
          gpsMes.offlineTime =
            day + "天" + hour + "时" + minute + "分" + second + "秒";
          offlineTimeoutArr.push(gpsMes);
        }
      }
      if (type == 2) {
        this.carOfflineTimeoutArr = offlineTimeoutArr;
      } else {
        this.gunOfflineTimeoutArr = offlineTimeoutArr;
      }
      this.offlineTimeoutArr = this.carOfflineTimeoutArr;
    },
    //函数功能：发送消息以后判断多久以后可以再次发送消息
    getCountDown(time) {
      let timeNow = Date.parse(new Date());
      let interval = parseInt((timeNow - parseInt(time)) / 1000);
      if (interval > 0) {
        let day = "00",
          hour = "00",
          minute = "00",
          second = "00";
        day = parseInt(interval / (24 * 3600));
        hour = parseInt((interval % (24 * 3600)) / 3600);
        minute = parseInt(((interval % (24 * 3600)) % 3600) / 60);
        second = parseInt(((interval % (24 * 3600)) % 3600) % 60);
        if (day) {
          return day + "天" + hour + "时" + minute + "分" + second + "秒";
        } else {
          return hour + "时" + minute + "分" + second + "秒";
        }
      } else {
        return 0;
      }
    },
    //函数作用：勾选车辆，定位到车辆位置，调整车辆层级，跟踪车辆
    //type 2:车 1：枪  trailToPos 有值表示是关闭实时轨迹开关操作
    getCarOrGunLastGps(type, trailToPos) {
      let that = this,
        elSelect = [];
      if (type == 2) {
        elSelect = this.elSelectCarArray;
      } else if (type == 1) {
        elSelect = this.elSelectGunArray;
      }
      lastgps(elSelect).then(res => {
        if (res && res.status == 0) {
          if (res.data !== [] && res.data) {
            //去除地图上多余的标注，增加新加的标注
            that.carOrGunSelectedChange(res.data, type);
            //调整视野
            this.setView();
            //改变设备层级
            this.setZindex(this.followCarOrGun);
          }
          //关闭实时轨迹开关操作操作不需要执行这个操作，否则跟踪状态会被取消
          if (!trailToPos) {
            $(".p" + that.richMarkerImeiIndex).click();
          }
        } else {
          this.$message.error(res.msg);
        }
      });
    },

    //去除makerIconArr中没被勾选的marker
    //carAndGunArr=['1','2','3','4','5']
    //markerArr=[{imei:'1',Icon:''},{imei:'3',Icon:''},{imei:'2',Icon:''},{imei:'7',Icon:''}]
    retainRepeat(carAndGunArr, markerArr) {
      let newMarkerArr = [];
      for (let i = 0; i < markerArr.length; i++) {
        let hasRepeat = false;
        for (let k = 0; k < carAndGunArr.length; k++) {
          if (markerArr[i].imei == carAndGunArr[k]) {
            newMarkerArr.push(markerArr[i]);
            hasRepeat = true;
            break;
          }
        }
        if (hasRepeat == false) {
          this.map.removeOverlay(markerArr[i].Icon);
          // this.map.removeOverlay(markerArr[i].IconLabel);

        }
      }
      this.makerIconArr = newMarkerArr;
    },

    //得到所有选中的车和枪的数据，并且初始化数据（经纬度，状态，时间等） type=1为枪支勾选变动，type=2为车辆勾选变动
    //this.gunIsOnlineArr是根据推送记录的是所有枪支的离线，上线，fake,电量为0这些状态（报警状态不包括）
    //this.carIsOnlineArr是根据推送记录的是所有车辆的离线，上线，fake,电量为0这些状态（报警状态不包括）

    formatMarkerArr(infos, carAndGun, type) {
      let arr = [];

      let gunOrCarIsOnlineObj = {};
      if (type == 1) {
        gunOrCarIsOnlineObj = this.gunIsOnlineArr;
      } else if (type == 2) {
        gunOrCarIsOnlineObj = this.carIsOnlineArr;
      }
      for (let i = 0; i < infos.length; i++) {
        let exType = 0;
        if (infos[i] == null) {
          continue;
        }
        //如果this.gunIsOnlineArr这个设备属性，代表没有记录代表没有收到过这个设备的上线离线等信息，这时候会初始化这个设备状态为0（离线）
        //车辆同理
        if (!gunOrCarIsOnlineObj.hasOwnProperty(infos[i].imei)) {
          gunOrCarIsOnlineObj[infos[i].imei] = 0;
        }
        if (
          gunOrCarIsOnlineObj[infos[i].imei] == 1 ||
          gunOrCarIsOnlineObj[infos[i].imei] == 5
        ) {
          exType = 4;
        } else if (gunOrCarIsOnlineObj[infos[i].imei] == 2) {
          exType = 0;
        } else if (gunOrCarIsOnlineObj[infos[i].imei] == 3) {
          exType = 1;
        } else if (gunOrCarIsOnlineObj[infos[i].imei] == 4) {
          exType = 3;
        }else if (gunOrCarIsOnlineObj[infos[i].imei] == 6) {
          exType = 6;
        }

        arr.push({
          message: {
            imei: infos[i].imei,
            type: type,
            lng: infos[i].lng,
            lat: infos[i].lat,
            time: infos[i].time,
            address: null,
            speed: infos[i].speed,
            battery: null,
            direction: null,
            exType: exType,
            number:infos[i].number,
            rfids:infos[i].rfids,
          }
        });
      }
      return arr;
    },
    //函数作用：当勾选的枪支或车辆发生变化时，去除地图上多余的标注，增加新加的标注
    //data为请求回来的数据  type=1为枪支勾选变动，type=2为车辆勾选变动
    carOrGunSelectedChange(data, type) {
      let that = this;
      let carAndGunSelectedArr = that.elSelectCarArray.concat(
        that.elSelectGunArray
      );
      let iconArr = [...that.makerIconArr];
      //初始化所有被选中的车或者是枪的数据（添加上一些属性，包括所有显示在弹窗的数据）
      let markerArr = that.formatMarkerArr(data, carAndGunSelectedArr, type);
      //在地图上去掉取消勾选的标注
      that.retainRepeat(carAndGunSelectedArr, that.makerIconArr);
      //以下是在地图上增加最新勾选的标注（如果之前就勾选过的则不作处理）
      for (let i = 0; i < markerArr.length; i++) {
        let hasIcon = iconArr.find(function (elem) {
          return elem.imei == markerArr[i].message.imei;
        });
        if (!hasIcon) {
          let arr = {};
          let arrtype = 0;
          if (markerArr[i].message.type == 2) {
            arr = that.carIsOnlineArr;
          } else if (markerArr[i].message.type == 1) {
            arr = that.gunIsOnlineArr;
          }
          if (arr[markerArr[i].message.imei] !== undefined) {
            arrtype = arr[markerArr[i].message.imei];
          }
          //在地图上添加marker
          that.addSelected(markerArr[i], arrtype);
        }
      }
    },
    //函数作用：判断是单个勾选还是多个勾选，调整视野
    setView() {
      let type = this.selectAllOrPart;
      let data = this.makerIconArr;
      let pointArr = [];
      let convertorData = [];
      if (this.selectAllOrPart == "all") {
        for (let i = 0; i < data.length; i++) {
          let ggPoint = new BMap.Point(data[i].lng, data[i].lat);
          pointArr.push(ggPoint);
        }
      } else if (this.selectAllOrPart == "part") {
        for (let i = 0; i < data.length; i++) {
          if (data[i].imei == this.selectImei) {
            let ggPoint = new BMap.Point(data[i].lng, data[i].lat);
            pointArr.push(ggPoint);
            break;
          }
        }
      }

      convertorData = Gps2Bds(pointArr);
      this.setZoom(convertorData);
      this.selectAllOrPart = null;
    },
    // 函数作用：根据点的数组自动调整缩放级别
    setZoom(bPoints) {
      let view = this.map.getViewport(eval(bPoints));
      let mapZoom = view.zoom;
      let centerPoint = view.center;
      this.map.centerAndZoom(centerPoint, mapZoom);
    },
    //设置跟踪和富标注
    initializeFollow() {
      // 如果复选框中没有富标注的imei，则删除富标注
      let myImei = this.richMarkerImeiIndex;
      let followCarOrGun = this.followCarOrGun;
      //重置富标注为-1，隐藏富标注
      if (
        myImei != undefined &&
        this.elSelectCarArray.indexOf(myImei) < 0 &&
        this.elSelectGunArray.indexOf(myImei) < 0
      ) {
        this.myRichMarkerDraged = false;
        this.myRichMarker.hide();
        this.richMarkerImeiIndex = -1;
      }
      //去掉跟踪
      if (
        followCarOrGun &&
        this.elSelectCarArray.indexOf(followCarOrGun) < 0 &&
        this.elSelectGunArray.indexOf(followCarOrGun) < 0
      ) {
        //tree-car-selected是被跟踪状态的标识
        $("span").removeClass("tree-car-selected");
        $(".trail").remove();
        this.followCarOrGun = null;
      }
    },
    //tree过滤功能
    filterNode(value, data, node) {
      value = value.trim();
      if (
        !value ||
        data.label.indexOf(value) !== -1 ||
        (data.devno !== undefined && data.devno.indexOf(value) !== -1)
      )
        return true;
      // 判断是不是选中节点的子节点
      return this.checkBelongToChooseNode(value, data, node);
    },
    // 判断传入的节点是不是选中节点的子节点
    checkBelongToChooseNode(value, data, node) {
      const level = node.level;
      // 如果传入的节点本身就是一级节点就不用校验了
      if (level === 1) return false;
      // 先取当前节点的父节点
      let parentData = node.parent;
      // 遍历当前节点的父节点
      let index = 0;
      while (index < level - 1) {
        // 如果匹配到直接返回
        if (
          parentData.data.label.indexOf(value) !== -1 ||
          (parentData.data.devno !== undefined &&
            parentData.data.devno.indexOf(value) !== -1)
        ) {
          return true;
        }
        // 否则的话再往上一层做匹配
        parentData = parentData.parent;
        index++;
      }
      // 没匹配到返回false
      return false;
    },
    // 显示菜单
    showMenu() {
      this.toshow = !this.toshow;
    },
    //设置设备层级，被跟踪的设备层级高于其他设备
    setZindex(followCarOrGun) {
      if (this.realTimeTrailValue == false) {
        let makerIcon = this.makerIconArr;
        for (let i = 0; i < makerIcon.length; i++) {
          if (followCarOrGun == makerIcon[i].imei) {
            makerIcon[i].Icon.setZIndex(2);
          } else {
            makerIcon[i].Icon.setZIndex(1);
          }
        }
      } else {
        let makerIcon = this.carMkObj;
        for (let item in makerIcon) {
          if (followCarOrGun == item) {
            makerIcon[item].setZIndex(2);
          } else {
            makerIcon[item].setZIndex(1);
          }
        }
      }
    },
    //通过设备号自动勾选tree节点
    //参数:info跟踪设备的信息 imei:跟踪设备号 hasChecked：跟踪或跟踪中
    checkNodes(info, imei, hasChecked) {
      let type; //车枪类型 1：枪 2：车
      let that = this;
      let array = []; //被勾选的设备
      let isChecked = false;
      let tree;
      let typeText;
      if (typeof info !== 'number') {
        typeText = this.formatCarOrGunType(info); //车或枪
        //this.carAndGunObj：用于判断这个设备是否在大队里面
        if (!this.carAndGunObj.hasOwnProperty(imei)) {
          return;
        }
        if (typeText == "车") {
          type = 2;
        } else if (typeText == "枪") {
          type = 1;
        }
      } else if (typeof info == 'number') {
        type = info;
      }

      if (type == 2) {
        tree = this.$refs.tree;
      } else if (type == 1) {
        tree = this.$refs.treeGun;
      }
      //获取被勾选的所有设备（车或枪）
      array = tree.getCheckedNodes(false, false).filter(item => {
        return item.type == "device";
      });
      // 代表该设备已被勾选
      for (let i = 0; i < array.length; i++) {
        if (array[i].id == imei) {
          isChecked = true;
        }
      }
      //如果已经被勾选，并且需要跟踪,且目前不是被跟踪设备，则点击 成为跟踪设备
      if (isChecked && hasChecked) {
        if (this.followCarOrGun !== imei) {
          $(".p" + imei).click();
        }
        return;
      }
      //如果该设备没被勾选，则添加到勾选数组里面
      if (isChecked == false) {
        array.push({
          id: imei,
          type: "device"
        });
        //重新设置被勾选的设备
        tree.setCheckedNodes(array, false);
      }

      // 获取被选中的车或枪
      let newArray = tree.getCheckedNodes(false, false);
      let carOrgunArray = [];

      let filterArray = newArray.filter(item => {
        return item.type == "device";
      });

      if (type == 2) {
        this.selectCarArray = filterArray;
        carOrgunArray = this.selectCarArray;
      } else if (type == 1) {
        this.selectGunArray = filterArray;
        carOrgunArray = this.selectGunArray;
      }

      let optionsArray = [];
      if (filterArray) {
        filterArray.forEach((item1, index1) => {
          optionsArray.push(item1.id);
        });
        if (type == 2) {
          this.elSelectCarArray = optionsArray;
        } else if (type == 1) {
          this.elSelectGunArray = optionsArray;
        }
      }
      //跟踪
      this.selectAllOrPart = "part";
