        that.myRichMarkerDraged = false;
        that.myRichMarker.hide();
      }
      //疑问：问什么勾选的时候只有是在下面就是实时轨迹的时候才执行点击事件？
      //答：由于实时轨迹是只有出现有了推送数据以后才会显示在地图上，因此只会在这个位置执行点击事件，把该设备变成被跟踪设备
      //疑问：那为什么实时位置不是在这个位置执行点击事件？有什么不同吗
      //答：假设在这个位置点击，此时会先执行点击事件，同时此时会请求获取设备最新位置及其他信息
      //但是由于请求还没回来，之前的点击事件里面会由于没有请求回来数据而让信息框不显示造成bug,因此实时位置状态会在请求最新位置的回调里面执行点击事件而不是在这
      if (this.realTimeTrailValue == true) {
        $(".p" + imei).click();
      }
      this.changeToRedAlarm();
    },

    //函数作用：
    //1.将发生报警需要改变为红色图标的设备记录下来（sessionStorage.aloneObj)
    //2.将需要从其他状态改变为红色报警状态的设备改变状态（实时位置中的设备或者实时轨迹中的设备都要改变）
    //3.将需要红色报警状态的改变其他状态的状态恢复为原来的状态（实时位置中的设备或者实时轨迹中的设备都要改变）
    changeToRedAlarm() {
      let that = this;
      let aloneGun1 = aloneGun; //报警枪支图标
      let aloneCar1 = aloneCar; //报警车辆图标
      let gpsCar1 = gpsCar; //离线车辆图标
      let gpsGun1 = gpsGun; //离线枪支图标
      this.aloneObj = {};
      let newWaringData1 = [...this.newWaringData]; //所有报警数据
      let makerIconArr = [...this.makerIconArr]; //所有已经被添加的实时位置设备

      //每次重新获取到所有报警显示为红色图标的设备
      for (let i = 0; i < newWaringData1.length; i++) {
        let status = newWaringData1[i].status;
        if (
          (newWaringData1[i].status == 10003 ||
            newWaringData1[i].status == 10011 ||
            newWaringData1[i].status == 10006 ||
            newWaringData1[i].status == 10007) &&
          newWaringData1[i].isCancled == false
        ) {
          //枪支脱离，一键报警， 车辆偏移,电量报警（后面函数addCarToMap会判断电量为0的时候才会改变颜色）
          //每一次推送上来以后报警数据都会有改变，有的已经取消报警，有的又已经变成了报警状态，因此每次都会重新判断哪些目前是处于报警状态的
          //因此this.aloneObj每次会清空重新记录
          if (!this.aloneObj.hasOwnProperty(newWaringData1[i].imei)) {
            this.aloneObj[newWaringData1[i].imei] = status;
          }
        }
      }
      sessionStorage.setItem("aloneObj", JSON.stringify(this.aloneObj));

      //设置实时位置设备为红色
      for (let k = 0; k < makerIconArr.length; k++) {
        let type1;
        if (makerIconArr[k].type == 2) {
          type1 = this.carIsOnlineArr[makerIconArr[k].imei];
        } else if (makerIconArr[k].type == 1) {
          type1 = this.gunIsOnlineArr[makerIconArr[k].imei];
        }
        this.addCarToMap({ imei: makerIconArr[k].imei, type: makerIconArr[k].type }, type1, 'alarm')
      }
      //设置实时轨迹设备为红色
      for (let item1 in this.carMkObj) {
        let type1, type2 = this.carAndGunObj[item1].type;
        if (type2 == 2) {
          type1 = this.carIsOnlineArr[item1];
        } else if (type2 == 1) {
          type1 = this.gunIsOnlineArr[item1];
        }
        this.addMarkerEnd(null, type2, { imei: item1, type: type2 }, type1, 'alarm')
      }
      // this.$forceUpdate();
    },
    //手动取消报警
    cancle(imei) {
      let warningData = this.newWaringData;

      for (let j = warningData.length - 1; j >= 0; j--) {
        if (
          warningData[j].status == 10011 &&
          warningData[j].imei == imei &&
          warningData[j].isCancled == false
        ) {
          warningData[j].isCancled = true;
          warningData.splice(j, 1);
          sessionStorage.setItem("warningData", JSON.stringify(warningData));
          this.$store.dispatch("realTimeMonitor/SAVE_WARNINGDATA");
          this.$store.commit("realTimeMonitor/ISADDNEWAlARM", false); //设置为false则不会导致报警数据变化而出现报警提示
          break;
        }
      }
    },
    //设置this.selectObj为所有被勾选的设备 键为设备号，值都为1
    getSelectObj() {
      let obj = {};
      for (let k = 0; k < this.selectCarArray.length; k++) {
        let id = this.selectCarArray[k].id;
        if (!obj.hasOwnProperty(id)) {
          obj[id] = 1;
        }
      }
      for (let j = 0; j < this.selectGunArray.length; j++) {
        let id = this.selectGunArray[j].id;
        if (!obj.hasOwnProperty(id)) {
          obj[id] = 1;
        }
      }
      this.selectObj = { ...obj };
    },
    //从所有的短报文消息里面或去每个终端最新的消息
    // 参数：data最新的消息在前面
    getOnly(data) {
      let obj = {};
      let arr = [];
      if (data !== null) {
        arr = [...data];
      }
      arr = arr.reduce(function (item, next) {
        obj[next.id] ? "" : (obj[next.id] = true && item.push(next));
        return item;
      }, []);
      return arr;
    },
    //添加北斗终端
    addTerminalIcon(message) {
      if (!message.lng || !message.lat) {
        return;
      }
      let that = this;
      let repeatIndex = null; //查找到的相同的imei号的索引
      let img = terminal; //显示的图标（在线车辆，离线车辆，在线枪支，离线枪支）
      let ggPoint = {}; //当前坐标点
      let convertorData = []; //转为百度坐标以后的坐标数组
      let Blng = null; //百度经度
      let Blat = null;
      let Icon = null; //被添加设备marker对象
      let point = {}; //被添加设备点
      let pointArr = [];
      for (let index = 0; index < that.terminalIcon.length; index++) {
        if (that.terminalIcon[index].id == message.id) {
          repeatIndex = index;
          break;
        }
      }

      ggPoint = new BMap.Point(message.lng, message.lat);
      pointArr.push(ggPoint);
      convertorData = Gps2Bds(pointArr);

      if (convertorData.length !== 0) {
        Blng = convertorData[0].lng;
        Blat = convertorData[0].lat;
        point = new BMap.Point(Blng, Blat);
        let marker = new BMap.Icon(img, this.bmapSize, {
          imageSize: this.bmapSize
        });

        //给lng,lat初始值，避免gps信号消失初次渲染不存在经纬度状况
        let lng = message.lng;
        let lat = message.lat;

        if (repeatIndex !== null) {
          that.terminalIcon[repeatIndex].Icon.setPosition(point);
          that.terminalIcon[repeatIndex].lng = message.lng;
          that.terminalIcon[repeatIndex].lat = message.lat;
          that.terminalIcon[repeatIndex].content = message.content;
          that.terminalIcon[repeatIndex].time = message.time;
          that.terminalIcon[repeatIndex].id = message.id;
          that.terminalIcon[repeatIndex].Blng = Blng;
          that.terminalIcon[repeatIndex].Blat = Blat;

          // 富标注实时定位
          if (message.id === this.richMarkerImeiIndex) {
            that.openInfoTerminal(that.terminalIcon[repeatIndex]);
          }
        } else {
          Icon = new BMap.Marker(point, {
            icon: marker,
            title: message.id
          });

          message.Icon = Icon;

          message.Blng = Blng;
          message.Blat = Blat;

          let a = { ...message };

          that.terminalIcon.push(a);
          that.addClickHandlerTerminal(message, Icon);
          that.map.addOverlay(Icon);
        }
      } else {
      }
    },
