      //处理后更新数据
      for (let i = data.length - 1; i >= 0; i--) {
        for (let j = 0; j < this.selectAlarmObjsBtn.length; j++) {
          let uuid=this.selectAlarmObjsBtn[j].info.uuid;
          let id=this.selectAlarmObjsBtn[j].info.id;
          if (data[i].info &&uuid&& uuid == data[i].info.uuid) {
            if (this.uuidObj.hasOwnProperty(uuid)) {
              delete this.uuidObj[data[i].info.uuid];
            }
            data.splice(i, 1);
          }else if(data[i].info &&id&& id == data[i].info.id){
            data.splice(i, 1);
          }
          
        }
      }
      sessionStorage.setItem("warningData", JSON.stringify(data));
      sessionStorage.setItem("uuidObj", JSON.stringify(this.uuidObj));
      this.$store.dispatch("realTimeMonitor/SAVE_WARNINGDATA");
      this.$store.commit("realTimeMonitor/ISADDNEWAlARM", false);
    },
    //过滤国外经纬度
    rayCasting(x, y, poly) {
      let px = x,
        py = y,
        flag = false;

      for (let i = 0, l = poly.length, j = l - 1; i < l; j = i, i++) {
        let sx = poly[i].lat,
          sy = poly[i].lng,
          tx = poly[j].lat,
          ty = poly[j].lng;

        // 点与多边形顶点重合
        if ((sx === px && sy === py) || (tx === px && ty === py)) {
          return "on";
        }

        // 判断线段两端点是否在射线两侧
        if ((sy < py && ty >= py) || (sy >= py && ty < py)) {
          // 线段上与射线 Y 坐标相同的点的 X 坐标
          let x = sx + ((py - sy) * (tx - sx)) / (ty - sy);

          // 点在多边形的边上
          if (x === px) {
            return "on";
          }

          // 射线穿过多边形的边界
          if (x > px) {
            flag = !flag;
          }
        }
      }

      // 射线穿过多边形边界的次数为奇数时点在多边形内
      return flag;
    },
